import React, { useState, useEffect } from "react";
import "react-responsive-modal/styles.css";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import { maintenanceLocationApi } from "views/examples/_actions";

const CreateRentRequestLocationSelector = (props) => {
  const [pickupDate, setPickupDate] = useState(new Date());
  const [pickupLocation, setPickupLocation] = useState("");

  const [dropoffDate, setDropoffDate] = useState(new Date());
  const [dropoffLocation, setDropoffLocation] = useState("");

  const [maintainanceLocations, setMaintainanceLocations] = useState([]);

  useEffect(() => {
    fetchMaintainanceLocations(); // eslint-disable-next-line
  }, []);

  const fetchMaintainanceLocations = async () => {
    try {
      let res = await maintenanceLocationApi();
      if (res?.data?.status === 0) {
        setMaintainanceLocations(res.data.data);
      }
    } catch (err) {}
  };

  const submitBtnHandler = (e) => {
    props?.history?.push({
      pathname: "/admin/rent_request/new",
    });
    console.log(
      " ~ file: rent-request-location-selector.js ~ line 30 ~ submitBtnHandler ~ props",
      props
    );
  };

  return (
    <div
      style={{
        display: "flex",
        gap: "100px",
      }}
    >
      <div style={{ flex: "1" }}>
        <Form>
          <div className="row">
            <div className="col text-center">
              <h3 className="mb-0 color-primary">Pick-up</h3>
            </div>
          </div>
          <div className="row">
            <div className="col-12">
              <FormGroup>
                <Label for="location">Location</Label>

                <Input
                  type="select"
                  name="location"
                  id="location"
                  onChange={(ev) => setPickupLocation(ev.target.value)}
                  value={pickupLocation}
                >
                  <option value={null}>Select location</option>
                  {maintainanceLocations.map((location) => {
                    return (
                      <option key={location.loc_id} value={location.loc_id}>
                        {location.loc_desc}
                      </option>
                    );
                  })}
                </Input>
              </FormGroup>
            </div>
            <div className="col-12">
              <FormGroup>
                <Label for="prefDate">Date / Time</Label>
                <Input
                  type="datetime-local"
                  name="prefDate"
                  id="prefDate"
                  placeholder="Date / Time"
                  onChange={(ev) => setPickupDate(ev.target.value)}
                  value={pickupDate}
                />
              </FormGroup>
            </div>
          </div>

          <div className="row">
            <div className="col text-center">
              <h3 className="mb-0 color-primary">Drop-off</h3>
            </div>
          </div>

          <div className="row">
            <div className="col-12">
              <FormGroup>
                <Label for="location">Location</Label>

                <Input
                  type="select"
                  name="location"
                  id="location"
                  onChange={(ev) => setDropoffLocation(ev.target.value)}
                  value={dropoffLocation}
                >
                  <option value={null}>Select location</option>
                  {maintainanceLocations.map((location) => {
                    return (
                      <option key={location.loc_id} value={location.loc_id}>
                        {location.loc_desc}
                      </option>
                    );
                  })}
                </Input>
              </FormGroup>
            </div>
            <div className="col-12">
              <FormGroup>
                <Label for="prefDate">Date / Time</Label>
                <Input
                  type="datetime-local"
                  name="prefDate"
                  id="prefDate"
                  placeholder="Date / Time"
                  onChange={(ev) => setDropoffDate(ev.target.value)}
                  value={dropoffDate}
                />
              </FormGroup>
            </div>
          </div>
          <div className="d-flex justify-content-center pt-4">
            <Button color="btn primary-bg" onClick={submitBtnHandler}>
              Choose your car
            </Button>
          </div>
        </Form>
      </div>
      <div style={{ flex: "1" }}>
        <img
          src="https://ebooking.avisksa.com/images/header-image.jpg"
          alt=""
        ></img>
      </div>
    </div>
  );
};

export default CreateRentRequestLocationSelector;
