import React, { useEffect, useState } from "react";
import UserHeader from "../../components/Headers/userHeader";
import { Card, CardHeader, Container, Row } from "reactstrap";
import ImgDocTable from "../../components/tables/imgDocTable";
import { logOut, handleArrayData } from "../../_shared/commonFunctions";
import { globalLoader } from "../../actions/commonActions";
import { vehicleDocumentListApi, vehicleDocumentRequestApi } from "./_actions";
import UploadDocModal from "../../components/Modals/uploadDocModal";
import { toast } from "react-toastify";

const ImgDocManagement = (props) => {
  const [currentData, setCurrentData] = useState(null);
  const [view, setView] = useState(false);
  const [openUploadDocModal, setOpenUploadDocModal] = useState(false);
  const [formData, setFormData] = useState(null);

  useEffect(() => {
    callBasicApi(); // eslint-disable-next-line
  }, []);

  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await vehicleDocumentListApi();
      if (res?.data?.status === 0) {
        // let arr = res?.data?.data;
        let data = await handleArrayData(res?.data?.data);
        setCurrentData(data);
        setView(true);
        globalLoader(false);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");

        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setCurrentData(null);
      } else {
      }
    } catch (err) {
      logOut(props?.history, "/auth/login");
      globalLoader(false);
    }
    globalLoader(false);
  };

  const uploadDocModalOpen = (obj = null) => {
    if (obj) {
      setFormData(obj);
      setOpenUploadDocModal(true);
    }
  };

  const uploadDocModalClose = async (obj = null) => {
    if (obj && obj?.DocumentDetails?.length > 0) {
      globalLoader(true);
      try {
        let res = await vehicleDocumentRequestApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
          toast.error(res?.data?.message);
        }
        globalLoader(false);
        callBasicApi();
      } catch (err) {
        setFormData(null);
        setOpenUploadDocModal(false);
        logOut(props?.history, "/auth/login");
        globalLoader(false);
      }
    }
    setFormData(null);
    setOpenUploadDocModal(false);
  };
  return (
    <>
      <UserHeader />
      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">
                    Image/Document Management
                  </h3>
                </Row>
              </CardHeader>
              {view ? (
                currentData && currentData?.length > 0 ? (
                  <ImgDocTable
                    data={currentData}
                    uploadDoc={uploadDocModalOpen}
                  />
                ) : (
                  <div className="px-3 pt-3">
                    <div className="no-data-available">No data available</div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>
      {openUploadDocModal && (
        <UploadDocModal
          open={openUploadDocModal}
          onCloseModal={uploadDocModalClose}
          formData={formData}
        />
      )}
    </>
  );
};

export default ImgDocManagement;
