import React, { useEffect, useState } from "react";
import UserHeader from "../../components/Headers/userHeader";
import { Card, CardHeader, Container, Row } from "reactstrap";
import { vehicleContractDriverDetailsApi } from "./_actions";
import { globalLoader } from "actions/commonActions";
import { sort } from "fast-sort";
import { logOut } from "_shared/commonFunctions";
import VehicleContractDriverTable from "../../components/tables/vehicleContractDriverTable";
import { connect } from "react-redux";

const VehicleContractDriver = (props) => {
  const { currentCompany } = props;
  const [currentDataList, setCurrentDataList] = useState([]);
  const [view, setView] = useState(false);

  useEffect(() => {
    callBasicApi();
    // eslint-disable-next-line
  }, [currentCompany]);

  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await vehicleContractDriverDetailsApi(currentCompany);
      if (res?.data?.status === 0) {
        let arr = res?.data?.data;
        let sortedArr = sort(arr).desc((u) => new Date(u?.from_dt));
        setCurrentDataList(sortedArr);
        setView(true);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");

        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setCurrentDataList(null);
      } else {
      }
      globalLoader(false);
    } catch (err) {
      logOut(props?.history, "/admin/login");
    }
  };

  return (
    <>
      <UserHeader />

      <Container className="mt--8 pb-3" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">Fleet Management</h3>
                  {/* <div className="col text-right">
                    <button className="btn btn-sm primary-bg">
                      Add Driver
                    </button>
                  </div> */}
                </Row>
              </CardHeader>
              {view ? (
                currentDataList && currentDataList.length > 0 ? (
                  <VehicleContractDriverTable tableData={currentDataList} />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
});

export default connect(mapStateProps)(VehicleContractDriver);
