import React, { useEffect, useState } from "react";
import UserHeader from "../../components/Headers/userHeader";
import { Card, CardHeader, Container, Row } from "reactstrap";
import DamageListModal from "components/Modals/damageListModal";
import ViewImageModal from "components/Modals/viewImageModal";
import { globalLoader } from "actions/commonActions";
import { damagePortalListApi, sendNotificationApi } from "./_actions";
import { sort } from "fast-sort";
import { toast } from "react-toastify";
import { logOut } from "_shared/commonFunctions";
import DamageTable from "../../components/tables/damagetable";
import { updateStatusApi } from "./_actions";
import StatusUpdateModal from "components/Modals/statusUpdateModal";
import { connect } from "react-redux";
import ViewMoreModal from "../../components/Modals/viewMoreModal";
import { getStatusArray } from "_shared/commonFunctions";
import DetailsModal from "../../components/Modals/detailsModal";
import { getPopupDetails } from "_shared/commonFunctions";
import ReplyNotification from "../../components/Modals/replyNotificationModal";

const DamageList = (props) => {
  const { userRights, currentCompany } = props;
  const [canEdit, setCanEdit] = useState(false);
  const [openDamageListModal, setOpenDamageListModal] = useState(false);
  const [openImageModal, setOpenImageModal] = useState(false);
  const [imgType, setImgType] = useState("");
  const [imageData, setImageData] = useState(null);
  const [currentDataList, setCurrentDatalist] = useState([]);
  const [view, setView] = useState(false);
  const [statusUpdate, setStatusUpdate] = useState(false);
  const [formData, setFormData] = useState(null);
  const [list, setList] = useState(null);
  const [desc, setDesc] = useState(null);
  const [openViewMore, setOpenViewMore] = useState(false);
  const [status, setStatus] = useState([]);
  const [data, setData] = useState(null);
  const [userModal, setUserModal] = useState(false);
  const [detailsTitle, setDetailsTitle] = useState("");
  const [notificationModalOpen, setNotificationModalOpen] = useState(false);

  useEffect(() => {
    callBasicApi();
    // eslint-disable-next-line
  }, [currentCompany]);

  useEffect(() => {
    window.history.replaceState(null, "");
  }, []);

  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await damagePortalListApi(currentCompany);
      if (res?.data?.status === 0) {
        let arr = res?.data?.data;
        let sortedArr = sort(arr).desc((u) => new Date(u?.dmg_datetime));
        setCurrentDatalist(sortedArr);
        setView(true);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");

        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setCurrentDatalist([]);
        //
      } else {
        globalLoader(false);
      }
      if (res?.data?.status !== 401) {
        let obj = await getStatusArray(4);
        if (obj) {
          setStatus(obj);
        } else {
          toast.error("Error in getting status");
        }
      }
      userRights?.forEach((item) => {
        if (item?.menu_type_id === "module_6") {
          if (item?.update === "1") {
            setCanEdit(true);
          }
        }
      });
      globalLoader(false);
    } catch (err) {
      logOut(props?.history, "/auth/login");
      globalLoader(false);
    }
  };

  const openImage = (obj) => {
    if (obj?.images[0] !== null) {
      setImageData(obj?.images);
      setImgType("Damage");
      setOpenImageModal(true);
    }
  };

  const closeImageModal = () => {
    setImageData("");
    setImgType("");
    setOpenImageModal(false);
  };

  const closeDamageListModal = (obj = null) => {
    if (obj) {
    }
    setOpenDamageListModal(false);
  };

  const openUpdateStatus = (obj) => {
    setFormData(obj);
    setStatusUpdate(true);
  };

  const viewMore = (obj, desc) => {
    setList(obj?.damage_details);
    setDesc(desc);
    setOpenViewMore(true);
  };
  const closeViewMore = () => {
    setList(null);
    setDesc(null);
    setOpenViewMore(false);
  };

  const closeUpdateStatus = async (status) => {
    if (status) {
      let obj = {
        type: "4",
        status: status,
        id: formData?.dmg_id,
      };
      globalLoader(true);
      try {
        let res = await updateStatusApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
        }
        callBasicApi();
      } catch (err) {
        logOut(props?.history, "auth/login");
        globalLoader(false);
      }
    }
    setStatusUpdate(false);
    setFormData(null);
  };

  const customerModalOpen = (obj, type) => {
    if (obj) {
      setData(getPopupDetails(obj, type));
      setDetailsTitle(type);
      setUserModal(true);
    }
  };

  const customerModalClose = () => {
    setData(null);
    setUserModal(false);
    setDetailsTitle("");
  };

  const openNotificationModal = (obj) => {
    let notiData = {
      user_id: obj.userid,
      tag: obj?.company_name,
      type: "4",
      typeDesc: "Damage",
    };
    setFormData(notiData);
    setNotificationModalOpen(true);
  };

  const closeNotificationModal = async (obj = null) => {
    if (obj) {
      globalLoader(true);
      try {
        let res = await sendNotificationApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
          toast.error(res?.data?.message);
        }
      } catch (err) {
        toast.error("Error while posting notification");
        console.log(err);
      }
      globalLoader(false);
      callBasicApi();
    }
    setFormData(null);
    setNotificationModalOpen(false);
  };

  return (
    <>
      <UserHeader />
      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">Self Inspection</h3>
                  {/* <div className="col text-right">
                    <button
                      className="btn btn-sm primary-bg"
                      onClick={() => setOpenDamageListModal(true)}
                    >
                      Add Damage List
                    </button>
                  </div> */}
                </Row>
              </CardHeader>
              {view ? (
                currentDataList && currentDataList?.length > 0 ? (
                  <DamageTable
                    statusList={status}
                    tableData={currentDataList}
                    openImage={openImage}
                    updateStatus={openUpdateStatus}
                    canEdit={canEdit}
                    viewMore={viewMore}
                    searchFilter={props?.location?.state?.fromDash ? "1" : null}
                    searchId={
                      props?.location?.state?.fromDash ? "dmg_status" : null
                    }
                    customerModalOpen={customerModalOpen}
                    sendNotification={openNotificationModal}
                  />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>
      {openViewMore && (
        <ViewMoreModal
          open={openViewMore}
          list={list}
          desc={desc}
          onCloseModal={closeViewMore}
        />
      )}
      {openDamageListModal && (
        <DamageListModal
          open={openDamageListModal}
          onCloseModal={closeDamageListModal}
        />
      )}

      {openImageModal && (
        <ViewImageModal
          open={openImageModal}
          onCloseModal={closeImageModal}
          data={imageData}
          imgType={imgType}
        />
      )}
      {statusUpdate && (
        <StatusUpdateModal
          open={statusUpdate}
          onCloseModal={closeUpdateStatus}
          statusList={status}
          formData={formData?.dmg_status}
        />
      )}
      {userModal && (
        <DetailsModal
          open={userModal}
          onCloseModal={customerModalClose}
          data={data}
          title={detailsTitle}
        />
      )}
      {notificationModalOpen && (
        <ReplyNotification
          open={notificationModalOpen}
          onCloseModal={closeNotificationModal}
          formData={formData}
        />
      )}
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
  userRights: state?.common?.roleData?.user_rights,
});

export default connect(mapStateProps)(DamageList);
