import React from "react";
import "react-responsive-modal/styles.css";
import { Modal } from "react-responsive-modal";

const ViewModal = (props) => {
  return (
    <>
      <Modal
        open={props?.open}
        onClose={() => props?.onCloseModal()}
        center
        closeOnOverlayClick={false}
        closeOnEsc={false}
        classNames={{ modal: "form-modal-own" }}
      >
        <div className="py-5 row">
          <h3 className="color-primary col-12 text-center">{props?.type}</h3>
          <ul className="list-unstyled pt-5 col-12">
            {props?.data.map((l, i) => (
              <li key={i} className="py-1 pl-5 ml-5">
                <span className="weight-600 pr-2 color-primary">
                  {l?.company_code}
                </span>
                <span className="pl-2">{l?.company_name}</span>
              </li>
            ))}
          </ul>
        </div>
      </Modal>
    </>
  );
};

export default ViewModal;
