import PrimaryButtonBordered from "components/button/primary-button-bordered";
import React, { useEffect, useState } from "react";
import CarBadge from "./car-badge";

const Car = ({ car, onSelectCar }) => {
  const [badges, setBadges] = useState([]);

  useEffect(() => {
    prepareBadges(car);
  }, [car]);

  const prepareBadges = (car) => {
    const seatBadge = {
      icon: "",
      title: `${car.no_of_seats} Seats`,
    };

    const doorsBadge = {
      icon: "",
      title: `${car.no_of_doors} Doors`,
    };

    const gearBadge = {
      icon: "",
      title: car.gear_type,
    };

    const bagBadge = {
      icon: "",
      title: "2 Bags",
    };

    const fuelBadge = {
      icon: "",
      title: car.rat_fuel,
    };

    const acBadge = {
      icon: "",
      title: "Air Conditioner",
    };

    setBadges([seatBadge, doorsBadge, gearBadge, bagBadge, fuelBadge, acBadge]);
  };

  const carSelectHandler = () => {};

  return (
    <div style={{ padding: "10px" }} className="col-4">
      <div style={{ padding: "10px", border: "1px solid #dedede" }}>
        <div className="row">
          <div className="col-12 font-18  weight-600">{car.rat_fleet_desc}</div>
        </div>

        <div className="col-12 row">
          <div className="font-16  weight-400 h-6">
            <span style={{ color: "#32325d9c", marginRight: "5px" }}>
              {" "}
              Example below:
            </span>
            {car.rat_vehicle_desc}
          </div>
        </div>

        <div className="d-flex" style={{ flexWrap: "wrap" }}>
          {badges.map((badge) => (
            <CarBadge title={badge.title} icon={badge.icon} />
          ))}
        </div>

        <div
          style={{ margin: "10px", display: "flex", justifyContent: "center" }}
        >
          <img
            style={{ width: "80%", height: "200px" }}
            src={car.image_url}
            alt="..."
          />
        </div>

        <div className="row col-12 font-24 weight-800">
          SAR {car.rate_daily}
        </div>
        <p style={{ color: "#32325d9c" }}>
          Pay on collection with free cancellation fee
        </p>
        <div className="col-12" style={{ marginBottom: "20px" }}>
          <PrimaryButtonBordered
            title="Choose this"
            onClick={carSelectHandler}
          />
        </div>
      </div>
    </div>
  );
};
export default Car;
