import React, { useState } from "react";
import "react-responsive-modal/styles.css";
import { Modal } from "react-responsive-modal";
import VehicleAssignmentModal from "./vehicleAssignmentModal";
import ViewImageModal from "./viewImageModal";

const VehicleDetailsModal = (props) => {
  const [vehicleAssign, setVehicleAssign] = useState(false);
  const [viewImageModal, setViewImageModal] = useState(false);
  const [imgData, setImgData] = useState(null);
  const imgType = "Vehicle Document";

  const closeVehicleAssign = (obj = null) => {
    if (obj) {
    }
    setVehicleAssign(false);
  };

  const viewImage = () => {
    let arr = [];
    props?.formData?.documents.forEach((img) => {
      let temp = { img_url: img?.doc_url };
      arr.push(temp);
    });
    setImgData(arr);
    setViewImageModal(true);
  };
  const closeImageModal = () => {
    setViewImageModal(false);
    setImgData(null);
  };
  return (
    <>
      <Modal
        open={props?.open}
        center
        onClose={() => props?.onCloseModal()}
        closeOnEsc={false}
        classNames={{ modal: "form-modal-own" }}
        styles={{ minWidth: "500px" }}
      >
        <h3 className="color-primary weight-800 text-center mt-4 pt-3 mr-4">
          VEHICLE DETAILS
        </h3>
        <ul className="details-wrapper list-unstyled">
          <li className="row pb-1">
            <div className="col-6 color-primary text-right weight-600">
              Plate Number
            </div>
            <div className="col-6">{props?.formData?.plate_no}</div>
          </li>
          <li className="row pb-1">
            <div className="col-6 color-primary text-right weight-600">
              Make
            </div>
            <div className="col-6">{props?.formData?.maker_desc}</div>
          </li>
          <li className="row pb-1">
            <div className="col-6 color-primary text-right weight-600">
              Model
            </div>{" "}
            <div className="col-6">{props?.formData?.makertype_desc}</div>
          </li>
          <li className="row pb-1">
            <div className="col-6 color-primary text-right weight-600">
              Variant
            </div>
            <div className="col-6">{props?.formData?.model_desc}</div>
          </li>
          <li className="row pb-1">
            <div className="col-6 color-primary text-right weight-600">
              Body Type
            </div>
            <div className="col-6">{props?.formData?.body_type}</div>
          </li>
          <li className="row pb-1">
            <div className="col-6 color-primary text-right weight-600">
              Transmission
            </div>
            <div className="col-6">{props?.formData?.Transmission}</div>
          </li>
          <li className="row pb-1">
            <div className="col-6 color-primary text-right weight-600">
              Number of Door
            </div>
            <div className="col-6">{props?.formData?.no_of_doors}</div>
          </li>
          <li className="row pb-1">
            <div className="col-6 color-primary text-right weight-600">
              Number of Seats
            </div>
            <div className="col-6">{props?.formData?.no_of_seats}</div>
          </li>
          <li className="row pb-1">
            <div className="col-6 color-primary text-right weight-600">
              Fuel Type
            </div>
            <div className="col-6">{props?.formData?.fuel_desc}</div>
          </li>
          <li className="row pb-1">
            <div className="col-6 color-primary text-right weight-600">
              Number of Gears
            </div>
            <div className="col-6">{props?.formData?.gear}</div>
          </li>
          <li className="row pb-1">
            <div className="col-6 color-primary text-right weight-600">
              MVPI Date
            </div>
            <div className="col-6">{props?.formData?.mvpi_date}</div>
          </li>{" "}
          <li className="row pb-1">
            <div className="col-6 color-primary text-right weight-600">
              Year
            </div>
            <div className="col-6">{props?.formData?.year}</div>
          </li>
          <li className="row pb-1">
            <div className="col-6 color-primary text-right weight-600">
              VIN Number
            </div>
            <div className="col-6">{props?.formData?.vin_no}</div>
          </li>
          <li className="row pb-1">
            <div className="col-6 color-primary text-right weight-600">
              Color
            </div>
            <div className="col-6">{props?.formData?.color_desc}</div>
          </li>
          {props?.formData?.documents[0] !== null && (
            <li className="d-flex justify-content-center my-3">
              {/* <div className="col-12 color-primary text-center weight-600 cursor-pointer pt-3">
                View Documents
              </div> */}

              <button onClick={viewImage} className="btn btn-sm primary-bg">
                View Documents
              </button>
            </li>
          )}
        </ul>
        <div className="d-flex justify-content-center my-5">
          <img alt="Vehicle" src={props?.formData?.image_url} />
        </div>
        {/* <div className="d-flex justify-content-center pt-3 mt-3">
          <div
            className="btn btn-sm primary-bg"
            onClick={() => setVehicleAssign(true)}
          >
            New Vehicle Assignment
          </div>
        </div> */}
      </Modal>
      {viewImageModal && (
        <ViewImageModal
          open={viewImageModal}
          onCloseModal={closeImageModal}
          data={imgData}
          imgType={imgType}
        />
      )}
      {vehicleAssign && (
        <VehicleAssignmentModal
          open={vehicleAssign}
          onCloseModal={closeVehicleAssign}
        />
      )}
    </>
  );
};

export default VehicleDetailsModal;
