import { globalLoader } from "actions/commonActions";
import UserHeader from "../../components/Headers/userHeader";
import ViewImageModal from "components/Modals/viewImageModal";
import AppUserTable from "components/tables/appUserTable";
import React, { useEffect, useState } from "react";
import { Card, CardHeader, Container, Row } from "reactstrap";
import { logOut } from "_shared/commonFunctions";
import { appUserListApi } from "./_actions";
import { connect } from "react-redux";

const AppUsers = (props) => {
  const { currentCompany } = props;
  const [view, setView] = useState(false);
  const [dataList, setDataList] = useState(null);
  const [imgType, setImgType] = useState("");
  const [viewImageModal, setViewImageModal] = useState(false);
  const [imgData, setImgData] = useState(null);

  useEffect(() => {
    callBasicApi(); // eslint-disable-next-line
  }, [currentCompany]);

  const callBasicApi = async () => {
    setView(false);
    try {
      globalLoader(true);
      let res = await appUserListApi(currentCompany);
      if (res?.data?.status === 0) {
        let arr = res?.data?.data;
        setDataList(arr);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");

        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setDataList(null);
        //
      } else {
      }
      globalLoader(false);
      setView(true);
    } catch (err) {
      if (err?.response?.status === 401) {
        logOut(props?.history, "/auth/login");
        globalLoader(false);
      } else {
        globalLoader(false);
      }
    }
  };

  const viewImage = (val = null, type = null) => {
    if (val) {
      let temp = [{ img_url: val }];
      setImgData(temp);
      setImgType(type);
      setViewImageModal(true);
    }
  };

  const closeImageModal = () => {
    setImgType("");
    setViewImageModal(false);
    setImgData(null);
  };

  return (
    <>
      <UserHeader />
      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">App Users</h3>
                </Row>
              </CardHeader>
              {view ? (
                dataList && dataList?.length > 0 ? (
                  <AppUserTable tableData={dataList} viewImage={viewImage} />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>
      {viewImageModal && (
        <ViewImageModal
          open={viewImageModal}
          onCloseModal={closeImageModal}
          data={imgData}
          imgType={imgType}
        />
      )}
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
});

export default connect(mapStateProps)(AppUsers);
