import { globalLoader } from "actions/commonActions";
import UserHeader from "../../components/Headers/userHeader";
import { sort } from "fast-sort";
import React, { useEffect, useState } from "react";
import { toast } from "react-toastify";
import { Card, CardHeader, Container, Row } from "reactstrap";
import { logOut } from "_shared/commonFunctions";
import { myBookingPortalListApi, sendNotificationApi } from "./_actions";
import RentTable from "../../components/tables/rentTable";
import StatusUpdateModal from "../../components/Modals/statusUpdateModal";
import { updateStatusApi } from "./_actions";
import { connect } from "react-redux";
import { getStatusArray } from "_shared/commonFunctions";
import DetailsModal from "../../components/Modals/detailsModal";
import { getPopupDetails } from "../../_shared/commonFunctions";
import ReplyNotification from "../../components/Modals/replyNotificationModal";
import PrimaryButton from "components/button/primary-button";

const RentRequest = (props) => {
  const { userRights, currentCompany } = props;
  const searchId = props?.location?.state?.searchId;
  const searchFilter = props?.location?.state?.searchFilter;
  const [canEdit, setCanEdit] = useState(false);
  const [view, setView] = useState(false);
  const [dataList, setDataList] = useState(null);
  const [statusUpdate, setStatusUpdate] = useState(false);
  const [formData, setFormData] = useState(null);
  const [status, setStatus] = useState([]);
  const [data, setData] = useState(null);
  const [userModal, setUserModal] = useState(false);
  const [detailsTitle, setDetailsTitle] = useState("");
  const [notificationModalOpen, setNotificationModalOpen] = useState(false);

  useEffect(() => {
    callBasicApi();
    // eslint-disable-next-line
  }, [currentCompany]);

  useEffect(() => {
    window.history.replaceState(null, "");
  }, []);

  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await myBookingPortalListApi(currentCompany);
      if (res?.data?.status === 0) {
        let arr = sort(res?.data?.data).desc((u) => u?.booking_id);
        setDataList(arr);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");

        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setDataList(null);
        // toast.error("Data not found");
      } else {
        globalLoader(false);
      }
      if (res?.data?.status !== 401) {
        let obj = await getStatusArray(6);
        if (obj) {
          setStatus(obj);
        } else {
          toast.error("Error in getting status");
        }
      }
      setView(true);
      userRights?.forEach((item) => {
        if (item?.menu_type_id === "module_13") {
          if (item?.update === "1") {
            setCanEdit(true);
          }
        }
      });
      globalLoader(false);
    } catch (err) {
      if (err?.response?.status === 401) {
        logOut(props?.history, "/auth/login");
        globalLoader(false);
      }
    }
  };

  const openUpdateStatus = (obj) => {
    setFormData(obj);
    setStatusUpdate(true);
  };

  const closeUpdateStatus = async (status) => {
    if (status) {
      let obj = {
        type: "6",
        status: status,
        id: formData?.booking_id,
      };
      globalLoader(true);
      try {
        let res = await updateStatusApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
        }
        callBasicApi();
      } catch (err) {
        logOut(props?.history, "auth/login");
        globalLoader(false);
      }
    }
    setStatusUpdate(false);
    setFormData(null);
  };

  const customerModalOpen = (obj, type) => {
    if (obj) {
      setData(getPopupDetails(obj, type));
      setDetailsTitle(type);
      setUserModal(true);
    }
  };

  const customerModalClose = () => {
    setData(null);
    setUserModal(false);
    setDetailsTitle("");
  };

  const openNotificationModal = (obj) => {
    let notiData = {
      user_id: obj.userid,
      tag: obj?.company_name,
      type: "6",
      typeDesc: "Rent",
    };
    setFormData(notiData);
    setNotificationModalOpen(true);
  };

  const closeNotificationModal = async (obj = null) => {
    if (obj) {
      globalLoader(true);
      try {
        let res = await sendNotificationApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
          toast.error(res?.data?.message);
        }
      } catch (err) {
        toast.error("Error while posting notification");
        console.log(err);
      }
      globalLoader(false);
      callBasicApi();
    }
    setFormData(null);
    setNotificationModalOpen(false);
  };

  return (
    <>
      <UserHeader />
      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">Rent Request</h3>
                  <div className="col text-right">
                    <PrimaryButton
                      title="Create Rent Request"
                      onClick={() => {
                        props?.history?.push({
                          pathname: "/admin/rent_request/new",
                        });
                      }}
                    />
                  </div>
                </Row>
              </CardHeader>
              {view ? (
                dataList && dataList.length > 0 ? (
                  <RentTable
                    tableData={dataList}
                    updateStatus={openUpdateStatus}
                    statusList={status}
                    canEdit={canEdit}
                    customerModalOpen={customerModalOpen}
                    searchFilter={searchFilter ? searchFilter : null}
                    searchId={searchId ? searchId : null}
                    sendNotification={openNotificationModal}
                  />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>
      {statusUpdate && (
        <StatusUpdateModal
          open={statusUpdate}
          onCloseModal={closeUpdateStatus}
          statusList={status}
          formData={formData?.rent_staus}
        />
      )}
      {userModal && (
        <DetailsModal
          open={userModal}
          onCloseModal={customerModalClose}
          data={data}
          title={detailsTitle}
        />
      )}
      {notificationModalOpen && (
        <ReplyNotification
          open={notificationModalOpen}
          onCloseModal={closeNotificationModal}
          formData={formData}
        />
      )}
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
  userRights: state?.common?.roleData?.user_rights,
});

export default connect(mapStateProps)(RentRequest);
