import React from "react";
import {
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  UncontrolledDropdown,
} from "reactstrap";
import TableContainer from "./_table";
import { DateRangeColumnFilter } from "./_filters";
import { tableDropdownData } from "_shared/commonFunctions";
import { SelectFilter } from "./_filters";

const TransactionTable = (props) => {
  const { statusList } = props;
  const columns = React.useMemo(
    () => [
      {
        Header: "",
        accessor: "dummy",
        disableFilters: true,
        disableSortBy: true,
      },
      {
        Header: () => <div className="color-primary">Actions</div>,
        accessor: "action",
        disableFilters: true,
        Cell: (cell) =>
          props?.canEdit && (
            <div className="text-left">
              <UncontrolledDropdown>
                <DropdownToggle
                  className="btn-icon-only text-light"
                  role="button"
                  size="sm"
                  color=""
                  onClick={(e) => e.preventDefault()}
                >
                  <i className="fas fa-ellipsis-v color-primary" />
                </DropdownToggle>
                <DropdownMenu className="dropdown-menu-arrow">
                  <DropdownItem
                    onClick={() => props?.updateStatus(cell?.row?.original)}
                  >
                    Update Status
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => props?.sendNotification(cell?.row?.original)}
                  >
                    Send Notification
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </div>
          ),
      },
      {
        Header: "Status",
        accessor: "bil_statusid",
        Cell: ({ row }) =>
          tableDropdownData(row?.original?.bil_statusid, statusList),
        Filter: (props) => SelectFilter(props, statusList),
        filter: "includes",
        disableSortBy: true,
      },
      {
        Header: "Invoice Number",
        accessor: "bil_docno",
      },
      { Header: "Plate No", accessor: "bil_plate" },
      {
        Header: "Due Date",
        accessor: "bil_datetime",
        Cell: ({ row }) =>
          new Date(row?.original?.bil_datetime).toLocaleDateString("en-Us", {
            day: "numeric",
            month: "short",
            year: "numeric",
            hour12: true,
            hour: "2-digit",
            minute: "2-digit",
          }),
        Filter: DateRangeColumnFilter,
        filter: "dateBetween",
        sortType: (rowA, rowB, id) => {
          if (new Date(rowA?.original[id]) > new Date(rowB?.original[id]))
            return 1;
          if (new Date(rowA?.original[id]) < new Date(rowB?.original[id]))
            return -1;
          return 0;
        },
      },
      {
        Header: "Category",
        accessor: "bil_categoryid",
        Cell: ({ row }) => row?.original?.bil_cat_desc,
        Filter: (props) => {
          let {
            column: { filterValue, setFilter, preFilteredRows, id, Header },
          } = props;
          const options = React.useMemo(() => {
            const options = new Set();
            preFilteredRows.forEach((row) => {
              options.add(row.values[id]);
            });
            return [...options.values()];
          }, [id, preFilteredRows]);
          let obj = [];
          options.forEach((option) => {
            preFilteredRows.forEach((row) => {
              if (row?.original?.bil_categoryid === option) {
                obj.push({ name: row?.original?.bil_cat_desc, id: option });
              }
            });
          });
          obj = obj.filter(
            (item, index, self) =>
              index === self.findIndex((t) => t.id === item.id)
          );
          return (
            <select
              className={`custom-select ${
                Header === "Maintenance Status" ? "w-80-perc" : ""
              }  table-filter-own custom-select-sm`}
              value={filterValue}
              onChange={(e) => {
                setFilter(e.target.value || undefined);
              }}
            >
              <option value="">All</option>
              {obj.map((option) => (
                <option key={option?.id} value={option.id}>
                  {option?.name}
                </option>
              ))}
            </select>
          );
        },
        filter: "includes",
        disableSortBy: true,
      },
      {
        Header: "Due Amount",
        accessor: "bil_amount_due",

        disableFilters: true,
      },
      {
        Header: "Total Amount",
        accessor: "bil_amount_tot",
        disableFilters: true,
      },

      { Header: "Make Model", accessor: "makemodel" },
      {
        Header: "",
        accessor: "dummy2",
        disableFilters: true,
        disableSortBy: true,
      },
      {
        Header: "ID",
        accessor: "bil_id",
      },
    ],
    // eslint-disable-next-line
    [props?.canEdit, statusList]
  );
  // eslint-disable-next-line
  const data = React.useMemo(() => props?.tableData, []);

  return <TableContainer columns={columns} data={data} />;
};

export default TransactionTable;
