import { globalLoader } from "actions/commonActions";
import UserHeader from "../../components/Headers/userHeader";
import { sort } from "fast-sort";
import React, { useEffect, useState } from "react";
import { toast } from "react-toastify";
import { Card, CardHeader, Container, Row } from "reactstrap";
import { logOut } from "../../_shared/commonFunctions";
import StatusUpdateModal from "components/Modals/statusUpdateModal";
import { updateStatusApi } from "./_actions";
import { chauffeurPortalServiceListApi } from "./_actions";
import ChauffeurServiceTable from "../../components/tables/chauffeurServiceTable";
import { connect } from "react-redux";
import { getStatusArray } from "_shared/commonFunctions";
import AddChauffeurServiceModal from "components/Modals/addChauffeurServiceModal";
import { createChauffeurServiceApi } from "./_actions";
import { updateChauffeurServiceApi } from "./_actions";

const ChauffeurService = (props) => {
  const { userRights, currentCompany } = props;
  const [canEdit, setCanEdit] = useState(false);
  const [view, setView] = useState(false);
  const [dataList, setDataList] = useState(null);
  const [statusUpdate, setStatusUpdate] = useState(false);
  const [formData, setFormData] = useState(null);
  const [canCreate, setCanCreate] = useState(false);
  const [status, setStatus] = useState([]);
  const [isEdit, setIsEdit] = useState(false);
  const [chauffeurServiceModalOpen, setChauffeurServiceModalOpen] =
    useState(false);

  useEffect(() => {
    callBasicApi();
    // eslint-disable-next-line
  }, [currentCompany]);

  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await chauffeurPortalServiceListApi(currentCompany);
      if (res?.data?.status === 0) {
        let arr = sort(res?.data?.data).desc((u) => u?.rate_type);
        setDataList(arr);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");
        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setDataList(null);
        //
      } else {
        globalLoader(false);
      }
      if (res?.data?.status !== 401) {
        let obj = await getStatusArray(13);
        if (obj) {
          setStatus(obj);
        } else {
          toast.error("Error in getting status");
        }
      }
      setView(true);
      userRights?.forEach((item) => {
        if (item?.menu_type_id === "module_19") {
          if (item?.update === "1") {
            setCanEdit(true);
          }
          if (item?.create === "1") {
            setCanCreate(true);
          }
        }
      });
      globalLoader(false);
    } catch (err) {
      if (err?.response?.status === 401) {
        logOut(props?.history, "/auth/login");
        globalLoader(false);
      }
    }
  };

  const openUpdateStatus = (obj) => {
    setFormData(obj);
    setStatusUpdate(true);
  };

  const closeUpdateStatus = async (status) => {
    if (status) {
      let obj = {
        type: "13",
        status: status,
        id: formData?.rate_type,
      };
      globalLoader(true);
      try {
        let res = await updateStatusApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
        }
        callBasicApi();
      } catch (err) {
        logOut(props?.history, "auth/login");
        globalLoader(false);
      }
    }
    setStatusUpdate(false);
    setFormData(null);
  };

  const openChauffeurServiceModal = (obj = null) => {
    if (obj) {
      setIsEdit(true);
      setFormData(obj);
    }
    setChauffeurServiceModalOpen(true);
  };

  const closeChauffeurServiceModal = async (obj = null) => {
    if (obj) {
      globalLoader(true);
      let res;
      if (isEdit) {
        res = await updateChauffeurServiceApi(obj);
      } else {
        res = await createChauffeurServiceApi(obj);
      }
      if (res?.data?.status === 0) {
        toast.success(res?.data?.message);
        window.location.reload();
        callBasicApi();
      } else {
        toast.error(res?.data?.message);
        globalLoader(false);
      }
    }
    setIsEdit(false);
    setFormData(null);
    setChauffeurServiceModalOpen(false);
  };

  return (
    <>
      <UserHeader />
      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">Chauffeur Service</h3>
                  {canCreate && (
                    <div className="col text-right">
                      <button
                        className="btn btn-sm primary-bg"
                        onClick={(ev) => {
                          ev.preventDefault();
                          openChauffeurServiceModal();
                        }}
                        type="button"
                      >
                        Add Chauffeur Service
                      </button>
                    </div>
                  )}
                </Row>
              </CardHeader>
              {view ? (
                dataList && dataList.length > 0 ? (
                  <ChauffeurServiceTable
                    tableData={dataList}
                    updateStatus={openUpdateStatus}
                    statusList={status}
                    canEdit={canEdit}
                    updateTable={openChauffeurServiceModal}
                  />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>
      {statusUpdate && (
        <StatusUpdateModal
          open={statusUpdate}
          onCloseModal={closeUpdateStatus}
          statusList={status}
          formData={formData?.rate_status}
        />
      )}

      {chauffeurServiceModalOpen && (
        <AddChauffeurServiceModal
          open={chauffeurServiceModalOpen}
          onCloseModal={closeChauffeurServiceModal}
          data={formData}
        />
      )}
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
  userRights: state?.common?.roleData?.user_rights,
});

export default connect(mapStateProps)(ChauffeurService);
