import React, { useEffect, useState } from "react";
import UserHeader from "../../components/Headers/userHeader";
import { Card, CardHeader, Container, Row } from "reactstrap";
import AddTransactionModal from "components/Modals/addTransactionModal";
import { billPortalListApi, sendNotificationApi } from "./_actions";
import TransactionTable from "../../components/tables/transactionTable";
import { sort } from "fast-sort";
import { globalLoader } from "actions/commonActions";
import { toast } from "react-toastify";
import { logOut } from "_shared/commonFunctions";
import { updateStatusApi } from "./_actions";
import StatusUpdateModal from "components/Modals/statusUpdateModal";
import { connect } from "react-redux";
import { getStatusArray } from "_shared/commonFunctions";
import ReplyNotification from "../../components/Modals/replyNotificationModal";

const InvoiceManagement = (props) => {
  const { userRights, currentCompany } = props;
  const [openTransactionModal, setOpenTransactionModal] = useState(false);
  const [statusUpdate, setStatusUpdate] = useState(false);
  const [formData, setFormData] = useState(null);
  const [canEdit, setCanEdit] = useState(false);
  const [status, setStatus] = useState([]);
  const [currentDataList, setCurrentDataList] = useState([]);
  const [view, setView] = useState(false);
  const [notificationModalOpen, setNotificationModalOpen] = useState(false);

  useEffect(() => {
    callBasicApi(); // eslint-disable-next-line
  }, [currentCompany]);

  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await billPortalListApi(currentCompany);
      if (res?.data?.status === 0) {
        let arr = res?.data?.data;
        let sortArr = sort(arr).desc((u) => new Date(u?.bil_datetime));
        setCurrentDataList(sortArr);
        setView(true);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");

        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setCurrentDataList(null);
        //
      } else {
        globalLoader(false);
      }
      if (res?.data?.status !== 401) {
        let obj = await getStatusArray(7);
        if (obj) {
          setStatus(obj);
        } else {
          toast.error("Error in getting status");
        }
      }
      userRights?.forEach((item) => {
        if (item?.menu_type_id === "module_5") {
          if (item?.update === "1") {
            setCanEdit(true);
          }
        }
      });
      globalLoader(false);
    } catch (err) {
      logOut(props?.history, "/auth/login");
      globalLoader(false);
    }
  };
  const closeTransactionModal = (obj = null) => {
    if (obj) {
    }
    setOpenTransactionModal(false);
  };

  const openUpdateStatus = (obj) => {
    setFormData(obj);
    setStatusUpdate(true);
  };

  const closeUpdateStatus = async (status) => {
    if (status) {
      let obj = {
        type: "7",
        status: status,
        id: formData?.bil_id,
      };
      globalLoader(true);
      try {
        let res = await updateStatusApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
        }
        callBasicApi();
      } catch (err) {
        logOut(props?.history, "auth/login");
        globalLoader(false);
      }
    }
    setStatusUpdate(false);
    setFormData(null);
  };

  const openNotificationModal = (obj) => {
    let notiData = {
      user_id: obj.bil_userid,
      tag: obj?.company_name,
      type: "7",
      typeDesc: "Transaction",
    };
    setFormData(notiData);
    setNotificationModalOpen(true);
  };

  const closeNotificationModal = async (obj = null) => {
    if (obj) {
      globalLoader(true);
      try {
        let res = await sendNotificationApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
          toast.error(res?.data?.message);
        }
      } catch (err) {
        toast.error("Error while posting notification");
        console.log(err);
      }
      globalLoader(false);
      callBasicApi();
    }
    setFormData(null);
    setNotificationModalOpen(false);
  };

  return (
    <>
      <UserHeader />
      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">Transactions</h3>
                  {/* <div className="col text-right">
                    <button
                      className="btn btn-sm primary-bg"
                      onClick={() => setOpenTransactionModal(true)}
                    >
                      Add Transaction
                    </button>
                  </div> */}
                </Row>
              </CardHeader>
              {view ? (
                currentDataList && currentDataList.length > 0 ? (
                  <TransactionTable
                    statusList={status}
                    tableData={currentDataList}
                    updateStatus={openUpdateStatus}
                    canEdit={canEdit}
                    sendNotification={openNotificationModal}
                  />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>
      {openTransactionModal && (
        <AddTransactionModal
          open={openTransactionModal}
          onCloseModal={closeTransactionModal}
        />
      )}
      {statusUpdate && (
        <StatusUpdateModal
          open={statusUpdate}
          onCloseModal={closeUpdateStatus}
          statusList={status}
          formData={formData?.bil_statusid}
        />
      )}
      {notificationModalOpen && (
        <ReplyNotification
          open={notificationModalOpen}
          onCloseModal={closeNotificationModal}
          formData={formData}
        />
      )}
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
  userRights: state?.common?.roleData?.user_rights,
});

export default connect(mapStateProps)(InvoiceManagement);
