import React, { useEffect } from "react";
import { Route, Redirect } from "react-router-dom";
import AdminLayout from "./layouts/admin";
import AuthLayout from "./layouts/auth";
import { ToastContainer } from "react-toastify";
import AvisForm from "layouts/avisForm";
import FAQ from "layouts/faq";
import FAQArabic from "layouts/faqArabic";
import Dashboard from "views/dashboard";

const App = () => {
  const mobileContent =
    "width=device-width,width=1500px, initial-scale=1, shrink-to-fit=no";
  const webContent = "width=device-width, initial-scale=1, shrink-to-fit=no";
  useEffect(() => {
    isMobileView();
  }, []);

  const isMobileView = () => {
    let metaTag = document.querySelector("#metaTag");
    if (
      /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
        navigator.userAgent
      )
    ) {
      metaTag.setAttribute("content", mobileContent);
    } else {
      metaTag.setAttribute("content", webContent);
    }
  };

  return (
    <>
      <ToastContainer
        position="top-right"
        autoClose={4000}
        hideProgressBar
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover={false}
      />
      <Route
        path="/admin/dashboard"
        render={(props) => <Dashboard {...props} />}
      />
      <Route path="/admin" render={(props) => <AdminLayout {...props} />} />
      <Route path="/auth" render={(props) => <AuthLayout {...props} />} />
      <Route path="/join-avis" render={(props) => <AvisForm {...props} />} />
      <Route path="/FAQS" exact render={(props) => <FAQ {...props} />} />
      <Route
        path="/FAQS-ar"
        exact
        render={(props) => <FAQArabic {...props} />}
      />
      <Route exact path="/admin">
        <Redirect to="/admin/index" />
      </Route>
      <Route exact path="/">
        <Redirect to="/auth/login" />
      </Route>
      <Route exact path="/auth">
        <Redirect to="/auth/login" />
      </Route>
    </>
  );
};

export default App;
