import { globalLoader } from "actions/commonActions";
import UserHeader from "../../components/Headers/userHeader";
import { sort } from "fast-sort";
import React, { useEffect, useState } from "react";
import { toast } from "react-toastify";
import { Card, CardHeader, Container, Row } from "reactstrap";
import { logOut } from "_shared/commonFunctions";
import StatusUpdateModal from "components/Modals/statusUpdateModal";
import {} from "./_actions";
import {
  portalMyBookingCarListApi,
  rateHireTypeApi,
  rateCategoryApi,
  updateStatusApi,
  createRentRateApi,
  updateRentRateApi,
} from "./_actions";
import RentRateTable from "../../components/tables/rentRateTable";
import { connect } from "react-redux";
import { getStatusArray, YEAR_LIST } from "../../_shared/commonFunctions";
import RentRateModal from "../../components/Modals/rentRateModal";
import { getBodyTypeApi } from "actions/commonActions";

const RentRate = (props) => {
  const { userRights, currentCompany } = props;
  const [canEdit, setCanEdit] = useState(false);
  const [view, setView] = useState(false);
  const [dataList, setDataList] = useState(null);
  const [statusUpdate, setStatusUpdate] = useState(false);
  const [formData, setFormData] = useState(null);
  const [status, setStatus] = useState([]);
  const [canCreate, setCanCreate] = useState(false);
  const [rentRateModalOpen, setRentRateModalOpen] = useState(false);
  const [bodyTypeList, setBodyTypeList] = useState([]);
  const [isEdit, setIsEdit] = useState(false);
  const [hireTypeList, setHireTypeList] = useState(null);
  const [categoryList, setCategoryList] = useState(null);
  const yearList = YEAR_LIST();

  useEffect(() => {
    callBasicApi();
    // eslint-disable-next-line
  }, [currentCompany]);

  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await portalMyBookingCarListApi(currentCompany);
      if (res?.data?.status === 0) {
        let arr = sort(res?.data?.data).desc((u) => u?.rat_id);
        setDataList(arr);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");

        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setDataList(null);
      } else {
        globalLoader(false);
      }
      if (res?.data?.status !== 401) {
        res = await getBodyTypeApi();
        if (res?.data?.status === 0) {
          let arr = res?.data?.data?.map((item) => {
            return { value: item?.bdy_id, label: item?.bdy_desc };
          });
          setBodyTypeList(arr);
        } else if (res?.data?.status === 401) {
          logOut(props?.history, "/auth/login");
          globalLoader(false);
        } else if (res?.data?.status === 404) {
          globalLoader(false);
          setView(true);
          setBodyTypeList([]);
        } else {
          globalLoader(false);
        }
      }
      if (res?.data?.status !== 401) {
        res = await rateHireTypeApi();
        if (res?.data?.status === 0) {
          let arr = res?.data?.data.map((item) => {
            return { value: item?.hir_status, label: item?.hir_desc };
          });
          setHireTypeList(arr);
        } else {
          toast.error("Error in fetching hire type");
        }
      }
      if (res?.data?.status !== 401) {
        res = await rateCategoryApi();
        if (res?.data?.status === 0) {
          let arr = res?.data?.data.map((item) => {
            return {
              value: item?.cat_code,
              label: `${item?.cat_desc} (${item?.cat_code})`,
            };
          });
          setCategoryList(arr);
        } else {
          toast.error("Error in fetching category type");
        }
      }
      if (res?.data?.status !== 401) {
        let obj = await getStatusArray(11);
        if (obj) {
          setStatus(obj);
        } else {
          toast.error("Error in getting status");
        }
      }
      setView(true);
      userRights?.forEach((item) => {
        if (item?.menu_type_id === "module_16") {
          if (item?.create === "1") {
            setCanCreate(true);
          }
          if (item?.update === "1") {
            setCanEdit(true);
          }
        }
      });
      globalLoader(false);
    } catch (err) {
      if (err?.response?.status === 401) {
        logOut(props?.history, "/auth/login");
        globalLoader(false);
      }
    }
  };

  const openUpdateStatus = (obj) => {
    setFormData(obj);
    setStatusUpdate(true);
  };

  const closeUpdateStatus = async (status) => {
    if (status) {
      let obj = {
        type: "11",
        status: status,
        id: formData?.rat_id,
      };
      globalLoader(true);
      try {
        let res = await updateStatusApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
        }
        callBasicApi();
      } catch (err) {
        logOut(props?.history, "auth/login");
        globalLoader(false);
      }
    }
    setStatusUpdate(false);
    setFormData(null);
  };

  const openRentRateModal = (obj = null) => {
    if (obj) {
      setFormData(obj);
      setIsEdit(true);
    }
    setRentRateModalOpen(true);
  };

  const closeRentRateModal = async (obj = null) => {
    if (obj) {
      globalLoader(true);
      let res;
      if (isEdit) {
        res = await updateRentRateApi(obj);
      } else {
        res = await createRentRateApi(obj);
      }
      if (res?.data?.status === 0) {
        toast.success(res?.data?.message);
        callBasicApi();
      } else {
        toast.error(res?.data?.message);
        globalLoader(false);
      }
    }
    setFormData(null);
    setRentRateModalOpen(false);
    setIsEdit(false);
  };

  return (
    <>
      <UserHeader />
      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">Rent Rate</h3>
                  {canCreate && (
                    <div className="col text-right">
                      <button
                        className="btn btn-sm primary-bg"
                        onClick={(ev) => {
                          ev.preventDefault();
                          openRentRateModal();
                        }}
                        type="button"
                      >
                        Add Rent Rate
                      </button>
                    </div>
                  )}
                </Row>
              </CardHeader>
              {view ? (
                dataList && dataList.length > 0 ? (
                  <RentRateTable
                    tableData={dataList}
                    updateStatus={openUpdateStatus}
                    editData={openRentRateModal}
                    statusList={status}
                    canEdit={canEdit}
                  />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>
      {statusUpdate && (
        <StatusUpdateModal
          open={statusUpdate}
          onCloseModal={closeUpdateStatus}
          statusList={status}
          formData={formData?.rac_status}
        />
      )}
      {rentRateModalOpen && (
        <RentRateModal
          open={rentRateModalOpen}
          onCloseModal={closeRentRateModal}
          data={formData}
          bodyTypeList={bodyTypeList}
          hireTypeList={hireTypeList}
          categoryList={categoryList}
          yearList={yearList}
        />
      )}
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
  userRights: state?.common?.roleData?.user_rights,
});

export default connect(mapStateProps)(RentRate);
