import React, { useMemo } from "react";
import {
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  UncontrolledDropdown,
} from "reactstrap";
import { tableDropdownData } from "_shared/commonFunctions";
import { SelectFilter } from "./_filters";
import { DateRangeColumnFilter } from "./_filters";
import TableContainer from "./_table";

const RentTable = (props) => {
  const { searchId, searchFilter, statusList } = props;

  const initState = {
    sortBy: [
      {
        id: "start_date",
        desc: true,
      },
    ],
    pageIndex: 0,
    pageSize: 20,
  };
  const columns = useMemo(
    () => [
      {
        Header: "",
        accessor: "dummy",
        disableFilters: true,
        disableSortBy: true,
      },
      {
        Header: () => <div className="color-primary">Actions</div>,
        accessor: "action",
        Cell: (cell) =>
          props?.canEdit && (
            <div className="text-left">
              <UncontrolledDropdown>
                <DropdownToggle
                  className="btn-icon-only text-light"
                  role="button"
                  size="sm"
                  color=""
                  onClick={(e) => e.preventDefault()}
                >
                  <i className="fas fa-ellipsis-v color-primary" />
                </DropdownToggle>
                <DropdownMenu className="dropdown-menu-arrow">
                  <DropdownItem
                    onClick={() => props?.updateStatus(cell?.row?.original)}
                  >
                    Update Status
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => props?.sendNotification(cell?.row?.original)}
                  >
                    Send Notification
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </div>
          ),
        disableFilters: true,
      },
      {
        Header: "Status",
        accessor: "rent_staus",
        Cell: ({ row }) =>
          tableDropdownData(row?.original?.rent_staus, statusList),
        Filter: (props) => SelectFilter(props, statusList),
        filter: "includes",
        disableSortBy: true,
      },
      {
        Header: "Company",
        accessor: "company_code",
        Cell: ({ row }) => (
          <div
            className={`color-primary ${
              row?.original?.company_code === "N/A" ? "" : "cursor-pointer"
            }`}
            onClick={() =>
              row?.original?.company_code !== "N/A" &&
              props?.customerModalOpen(row?.original, "company")
            }
          >
            {row?.original?.company_code}
          </div>
        ),
      },
      {
        Header: "User",
        accessor: "national_id",
        Cell: ({ row }) => (
          <div
            className={`color-primary ${
              row?.original?.national_id === "N/A" ? "" : "cursor-pointer"
            }`}
            onClick={() =>
              row?.original?.national_id !== "N/A" &&
              props?.customerModalOpen(row?.original, "customer")
            }
          >
            {row?.original?.national_id}
          </div>
        ),
      },
      {
        Header: "Start date and time",
        accessor: "start_date",
        Cell: ({ row }) =>
          new Date(row?.original?.start_date).toLocaleString("en-Us", {
            day: "numeric",
            month: "short",
            year: "numeric",
            hour12: true,
            hour: "2-digit",
            minute: "2-digit",
          }),
        Filter: DateRangeColumnFilter,
        filter: "dateBetween",
        sortType: (rowA, rowB, id) => {
          if (new Date(rowA?.original[id]) > new Date(rowB?.original[id]))
            return 1;
          if (new Date(rowA?.original[id]) < new Date(rowB?.original[id]))
            return -1;
          return 0;
        },
      },
      {
        Header: "End date and time",
        accessor: "end_date",
        Cell: ({ row }) =>
          new Date(row?.original?.end_date).toLocaleString("en-Us", {
            day: "numeric",
            month: "short",
            year: "numeric",
            hour12: true,
            hour: "2-digit",
            minute: "2-digit",
          }),
        Filter: DateRangeColumnFilter,
        filter: "dateBetween",
        sortType: (rowA, rowB, id) => {
          if (new Date(rowA?.original[id]) > new Date(rowB?.original[id]))
            return 1;
          if (new Date(rowA?.original[id]) < new Date(rowB?.original[id]))
            return -1;
          return 0;
        },
      },
      {
        Header: "Pick up",
        accessor: "start_location_address",
        Cell: ({ row }) => (
          <span className="table-text">
            {row?.original?.start_location_address}
          </span>
        ),
      },
      {
        Header: "Drop off",
        accessor: "end_location_address",
        Cell: ({ row }) => (
          <span className="table-text">
            {row?.original?.end_location_address}
          </span>
        ),
      },
      {
        Header: "type",
        accessor: "car_type",
      },
      {
        Header: "Makemodel",
        accessor: "make_model",
        Cell: ({ row }) => (
          <div className="table-text">{row?.original?.make_model}</div>
        ),
        disableFilters: true,
      },
      {
        Header: "Transmission",
        accessor: "transmission",
      },
      {
        Header: "Seats",
        accessor: "seats",
      },
      {
        Header: "Fuel",
        accessor: "fuel",
      },
      {
        Header: "Rate",
        accessor: "rate",
        Cell: ({ row }) => (
          <div className="d-flex justify-content-end">
            <span>{row?.original?.rate}</span>
          </div>
        ),
        disableFilters: true,
      },
      {
        Header: "Duration",
        accessor: "duration",
        Cell: ({ row }) => (
          <div className="d-flex justify-content-end pr-2">
            <span>{row?.original?.duration}</span>
          </div>
        ),
        disableFilters: true,
      },
      {
        Header: "Total",
        accessor: "total",
        Cell: ({ row }) => (
          <div className="d-flex justify-content-end pr-2">
            <span>{row?.original?.total}</span>
          </div>
        ),
        disableFilters: true,
      },
      {
        Header: "ID",
        accessor: "booking_id",
      },
      {
        Header: "",
        accessor: "dummy2",
        disableFilters: true,
        disableSortBy: true,
      },
    ],
    // eslint-disable-next-line
    [props?.canEdit, statusList]
  );
  // eslint-disable-next-line
  const data = useMemo(() => props?.tableData, []);

  return (
    <TableContainer
      searchFilter={searchFilter}
      searchId={searchId}
      columns={columns}
      data={data}
      state={initState}
    />
  );
};

export default RentTable;
