import React from "react";
import { Card, CardBody, CardHeader } from "reactstrap";

const InvoiceCard = (props) => {
  const { invoiceCard, type } = props;
  return (
    <Card className="dashboard-card-own revenue-card-wrapper">
      <CardHeader className="border-bottom-0 font-13 color-primary weight-600 pb-1">
        {type} Invoices
      </CardHeader>
      <CardBody className="pt-3">
        {invoiceCard && invoiceCard.length > 0 ? (
          <>
            <div className="table-responsive revenue-table-wrapper">
              <table className="table revenue-table table-hover text-left">
                {type === "Pending" ? (
                  <>
                    <thead className="thead-own px-0">
                      <tr>
                        <td className="pr-1">Plate No</td>
                        <td className="px-1">Description</td>
                        <td className="px-1">Total Amount</td>
                        <td className="px-1">Due Amount</td>
                        <td className="pl-1">Due Date</td>
                      </tr>
                    </thead>
                    <tbody>
                      {invoiceCard.map((data, index) => {
                        return (
                          <tr key={index}>
                            <td className="pr-1">{data?.bil_plate}</td>
                            <td className="px-1">{data?.cat_desc}</td>
                            <td className="text-right">
                              {data?.bil_amount_tot}
                            </td>
                            <td className="text-right">
                              {data?.bil_amount_due}
                            </td>
                            <td className="pl-1">
                              {new Date(data?.bil_datetime).toLocaleString(
                                "en-Us",
                                {
                                  day: "numeric",
                                  month: "short",
                                  year: "numeric",
                                }
                              )}
                            </td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </>
                ) : (
                  <>
                    <thead className="thead-own px-0">
                      <tr>
                        <td className="pr-1">Invoice No</td>
                        <td className="px-1">Company</td>
                        <td className="px-1">Amount</td>
                      </tr>
                    </thead>
                    <tbody>
                      {invoiceCard.map((data, index) => {
                        return (
                          <tr key={index}>
                            <td className="pr-1">{data?.blp_docno}</td>
                            <td className="px-1">{data?.comp_name}</td>
                            <td className="text-right">
                              {data?.bil_amount_due}
                            </td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </>
                )}
              </table>
            </div>
            {type === "Pending" && (
              <div className="font-13 color-primary weight-600 d-flex justify-content-between pt-3">
                <div>
                  {InvoiceCard?.length} Pending{" "}
                  {InvoiceCard?.length === 1 ? "Payment" : "Payments"}
                </div>
                <div>
                  <button
                    className="listing-btn"
                    onClick={() =>
                      props?.history?.push("/admin/invoice_management")
                    }
                  >
                    View All <i className="fas fa-arrow-right"></i>
                  </button>
                </div>
              </div>
            )}
          </>
        ) : (
          <div className="mt-5 pt-4 color-primary d-flex justify-content-center align-items-center">
            Data Not Available
          </div>
        )}
      </CardBody>
    </Card>
  );
};

export default InvoiceCard;
