import React, { useEffect, useState } from "react";
import UserHeader from "../../components/Headers/userHeader";
import { sort } from "fast-sort";
import { toast } from "react-toastify";
import { Card, CardHeader, Container, Row } from "reactstrap";
import StatusUpdateModal from "../../components/Modals/statusUpdateModal";
import ChauffeurRateTable from "../../components/tables/chauffeurRateTable";
import { chauffeurRateApi, updateStatusApi } from "./_actions";
import { connect } from "react-redux";
import ChauffeurRateModal from "../../components/Modals/chauffeurRateModal";
import {
  getRateTypeApi,
  getBodyTypeApi,
  globalLoader,
} from "../../actions/commonActions";
import {
  YEAR_LIST,
  getStatusArray,
  logOut,
} from "../../_shared/commonFunctions";
import { createChauffeurRateApi, updateChauffeurRateApi } from "./_actions";

const ChauffeurRate = (props) => {
  const { userRights, currentCompany } = props;
  const [canEdit, setCanEdit] = useState(false);
  const [view, setView] = useState(false);
  const [dataList, setDataList] = useState(null);
  const [statusUpdate, setStatusUpdate] = useState(false);
  const [formData, setFormData] = useState(null);
  const [status, setStatus] = useState([]);
  const [canCreate, setCanCreate] = useState(false);
  const [chauffeurRateModalOpen, setChauffeurRateModalOpen] = useState(false);
  const [rateTypeList, setRateTypeList] = useState([]);
  const [bodyTypeList, setBodyTypeList] = useState([]);
  const [isEdit, setIsEdit] = useState(false);
  const yearList = YEAR_LIST();

  useEffect(() => {
    callBasicApi();
    // eslint-disable-next-line
  }, [currentCompany]);

  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await chauffeurRateApi(currentCompany);
      if (res?.data?.status === 0) {
        let arr = sort(res?.data?.data).desc((u) => u?.chr_id);
        setDataList(arr);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");
        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setDataList(null);
        //
      } else {
        globalLoader(false);
      }
      if (res?.data?.status !== 401) {
        res = await getRateTypeApi();
        if (res?.data?.status === 0) {
          let arr = res?.data?.data?.map((item) => {
            return { value: item?.rate_type, label: item?.rate_type_desc };
          });
          setRateTypeList(arr);
        } else if (res?.data?.status === 401) {
          logOut(props?.history, "/auth/login");
          globalLoader(false);
        } else if (res?.data?.status === 404) {
          globalLoader(false);
          setView(true);
          setRateTypeList([]);
          //
        } else {
          globalLoader(false);
        }
      }
      if (res?.data?.status !== 401) {
        res = await getBodyTypeApi();
        if (res?.data?.status === 0) {
          let arr = res?.data?.data?.map((item) => {
            return { value: item?.bdy_id, label: item?.bdy_desc };
          });
          setBodyTypeList(arr);
          // res?.data?.data.forEach((item) => {
          //   setBodyTypeList((oldList) => [
          //     ...oldList,
          //     { value: item?.bdy_id, label: item?.bdy_desc },
          //   ]);
          // });
        } else if (res?.data?.status === 401) {
          logOut(props?.history, "/auth/login");
          globalLoader(false);
        } else if (res?.data?.status === 404) {
          globalLoader(false);
          setView(true);
          setBodyTypeList([]);
          //
        } else {
          globalLoader(false);
        }
      }
      if (res?.data?.status !== 401) {
        let obj = await getStatusArray(12);
        if (obj) {
          setStatus(obj);
        } else {
          toast.error("Error in getting status");
        }
      }
      setView(true);
      userRights?.forEach((item) => {
        if (item?.menu_type_id === "module_15") {
          if (item?.update === "1") {
            setCanEdit(true);
          }
          if (item?.create === "1") {
            setCanCreate(true);
          }
        }
      });
      globalLoader(false);
    } catch (err) {
      if (err?.response?.status === 401) {
        logOut(props?.history, "/auth/login");
        globalLoader(false);
      }
    }
  };

  const openUpdateStatus = (obj) => {
    setFormData(obj);
    setStatusUpdate(true);
  };

  const closeUpdateStatus = async (status) => {
    if (status) {
      let obj = {
        type: "12",
        status: status,
        id: formData?.chr_id,
      };
      globalLoader(true);
      try {
        let res = await updateStatusApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
        }
        callBasicApi();
      } catch (err) {
        logOut(props?.history, "auth/login");
        globalLoader(false);
      }
    }
    setStatusUpdate(false);
    setFormData(null);
  };

  const openChauffeurRateModal = (obj = null) => {
    if (obj) {
      setFormData(obj);
      setIsEdit(true);
    }
    setChauffeurRateModalOpen(true);
  };

  const closeChauffeurRateModal = async (obj = null) => {
    if (obj) {
      globalLoader(true);
      let res;
      if (isEdit) {
        res = await updateChauffeurRateApi(obj);
      } else {
        res = await createChauffeurRateApi(obj);
      }
      if (res?.data?.status === 0) {
        toast.success(res?.data?.message);
        callBasicApi();
      } else {
        toast.error(res?.data?.message);
        globalLoader(false);
      }
    }
    setFormData(null);
    setChauffeurRateModalOpen(false);
    setIsEdit(false);
  };

  return (
    <>
      <UserHeader />
      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">Chauffeur Rate</h3>
                  {canCreate && (
                    <div className="col text-right">
                      <button
                        className="btn btn-sm primary-bg"
                        onClick={(ev) => {
                          ev.preventDefault();
                          openChauffeurRateModal();
                        }}
                        type="button"
                      >
                        Add Chauffeur Rate
                      </button>
                    </div>
                  )}
                </Row>
              </CardHeader>
              {view ? (
                dataList && dataList.length > 0 ? (
                  <ChauffeurRateTable
                    tableData={dataList}
                    updateStatus={openUpdateStatus}
                    statusList={status}
                    editData={openChauffeurRateModal}
                    canEdit={canEdit}
                    rateTypeList={rateTypeList}
                  />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>
      {statusUpdate && (
        <StatusUpdateModal
          open={statusUpdate}
          onCloseModal={closeUpdateStatus}
          statusList={status}
          formData={formData?.char_status}
        />
      )}
      {chauffeurRateModalOpen && (
        <ChauffeurRateModal
          open={chauffeurRateModalOpen}
          onCloseModal={closeChauffeurRateModal}
          data={formData}
          rateTypeList={rateTypeList}
          bodyTypeList={bodyTypeList}
          yearList={yearList}
        />
      )}
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
  userRights: state?.common?.roleData?.user_rights,
});

export default connect(mapStateProps)(ChauffeurRate);
