import React from "react";

import { useEffect, useState } from "react";
import { portalMyBookingCarListApi } from "views/examples/_actions";
import Car from "./car";
import HorizontalStepper from "components/stepper/horizontal-stepper";
import AccordionHorizontal from "components/accordion-horizontal/accordion-horizontal";
import BookingHeader from "./booking-header";

const CreateRentRequestCarSelector = (props) => {
  const [cars, setCars] = useState([]);
  const [selectedCar, setSelctedCar] = useState(null);

  useEffect(() => {
    getRentCarList();
  }, []);

  const getRentCarList = async () => {
    try {
      let res = await portalMyBookingCarListApi();
      if (res?.data?.status === 0) {
        setCars(res?.data?.data);
      }
    } catch (err) {}
  };

  const selectCarHandler = (car) => {
    setSelctedCar(car);
  };

  return (
    <>
      <div
        className="row"
        style={{ marginLeft: "100px", marginTop: "150px", marginRight: "50px" }}
      >
        <BookingHeader activeStep={1} />

        <div
          className="row col-12"
          style={{ display: "flex", flexDirection: "column" }}
        >
          <h1 className="weight-800">Select your preferred vehicle type</h1>
          <h5 style={{ color: "#32325d9c" }}>
            *When you confirm your booking and choose a vehicle type, we will
            inform you of the vehicle model(s) available.
          </h5>
        </div>
        <div className="row">
          {cars.map((car) => {
            return <Car car={car} onSelectCar={selectCarHandler} />;
          })}
        </div>
      </div>
    </>
  );
};

export default CreateRentRequestCarSelector;
