import React, { useState, useEffect } from "react";
import UserHeader from "../../components/Headers/userHeader";
import { Card, CardHeader, Container, Row } from "reactstrap";
import AddUserModal from "../../components/Modals/addUserModal";
import ViewImageModal from "../../components/Modals/viewImageModal";
import UserTable from "../../components/tables/userTable";
import { sort } from "fast-sort";
import ConfirmModal from "../../components/Modals/confirmModal";
import { globalLoader } from "actions/commonActions";
import {
  usageListApi,
  userPortalListApi,
  createUserApi,
  updateUserApi,
  updateStatusApi,
  companyListApi,
} from "./_actions";
import { toast } from "react-toastify";
import { logOut } from "../../_shared/commonFunctions";
import StatusUpdateModal from "components/Modals/statusUpdateModal";
import { connect } from "react-redux";
import ViewModal from "components/Modals/viewModal";
import { roleMastterUserApi } from "./_actions";
import { designationUserApi } from "./_actions";
import { profileDetailsApi } from "./_actions";
import { profileApiData } from "actions/commonActions";
import { getStatusArray } from "_shared/commonFunctions";

const UserList = (props) => {
  const { userRights, currentCompany } = props;
  const [openUserModal, setOpenUserModal] = useState(false);
  const [statusUpdate, setStatusUpdate] = useState(false);
  const [formData, setFormData] = useState(null);
  const [type, setType] = useState(null);
  const [openViewModal, setOpenViewModal] = useState(false);
  const [openImageModal, setOpenImageModal] = useState(false);
  const [imageData, setImageData] = useState(null);
  const [imgType, setImgType] = useState("");
  const [currentDataList, setCurrentDataList] = useState([]);
  const [isEdit, setIsEdit] = useState(false);
  const [confirmModal, setConfirmModal] = useState(false);
  const [view, setView] = useState(false);
  const [designationList, setDesignationList] = useState(null);
  const [companyList, setCompanyList] = useState(null);
  const [roleList, setRoleList] = useState(null);
  const [usageList, setUsageList] = useState(null);
  const [emailList, setEmailList] = useState(null);
  const [canCreate, setCanCreate] = useState(false);
  const [canEdit, setCanEdit] = useState(false);
  const [status, setStatus] = useState([]);

  useEffect(() => {
    callBasicApi();
    // eslint-disable-next-line
  }, [currentCompany]);

  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await userPortalListApi(currentCompany);
      if (res?.data?.status === 0) {
        let arr = res?.data?.data;
        let sortedArr = sort(arr).desc((u) => parseInt(u?.user_id));
        let temp = sortedArr.map((arr) => arr?.email);
        setEmailList(temp);
        setCurrentDataList(sortedArr);
        setView(true);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setCurrentDataList(null);
        //
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");

        globalLoader(false);
      } else {
        globalLoader(false);
      }
      if (res?.data?.status !== 401) {
        res = await designationUserApi();
        if (res?.data?.status === 0) {
          let arr = res?.data?.data;
          let tempArr = [];
          arr.forEach((data) => {
            let temp = {
              label: data?.pdg_desc,
              value: data?.pdg_id,
            };
            tempArr.push(temp);
          });
          setDesignationList(tempArr);
        } else if (res?.data?.status === 404) {
          globalLoader(false);
          setView(true);
          setCurrentDataList(null);
        } else if (res?.data?.status === 401) {
          logOut(props?.history, "/auth/login");
          globalLoader(false);
        } else {
          globalLoader(false);
        }
      }
      if (res?.data?.status !== 401) {
        res = await companyListApi();
        if (res?.data?.status === 0) {
          let arr = res?.data?.data;
          let tempArr = [];
          arr.forEach((data) => {
            let temp = { value: data?.company_code, label: data?.company_name };
            tempArr.push(temp);
          });
          setCompanyList(tempArr);
        } else if (res?.data?.status === 404) {
          globalLoader(false);
          setView(true);
          setCurrentDataList(null);
        } else if (res?.data?.status === 401) {
          logOut(props?.history, "/auth/login");
          globalLoader(false);
        } else {
          globalLoader(false);
        }
      }
      if (res?.data?.status !== 401) {
        res = await roleMastterUserApi();
        if (res?.data?.status === 0) {
          let arr = res?.data?.data;
          let tempArr = [];
          arr.forEach((data) => {
            let temp = { value: data?.role_id, label: data?.role_name };
            tempArr.push(temp);
          });
          setRoleList(tempArr);
        } else if (res?.data?.status === 404) {
          globalLoader(false);
          setView(true);
          setCurrentDataList(null);
        } else if (res?.data?.status === 401) {
          logOut(props?.history, "/auth/login");
          globalLoader(false);
        } else {
          globalLoader(false);
        }
      }
      if (res?.data?.status !== 401) {
        res = await usageListApi();
        if (res?.data?.status === 0) {
          let arr = res?.data?.data;
          let tempArr = [];
          arr.forEach((data) => {
            let temp = { value: data?.put_id, label: data?.put_desc };
            tempArr.push(temp);
          });
          setUsageList(tempArr);
        } else if (res?.data?.status === 404) {
          globalLoader(false);
          setView(true);
          setCurrentDataList(null);
        } else if (res?.data?.status === 401) {
          logOut(props?.history, "/auth/login");
          globalLoader(false);
        } else {
          globalLoader(false);
        }
      }

      if (res?.data?.status !== 401) {
        let obj = await getStatusArray(17);
        if (obj) {
          setStatus(obj);
        } else {
          toast.error("Error in getting status");
        }
      }
      userRights?.forEach((item) => {
        if (item?.menu_type_id === "module_1") {
          if (item?.create === "1") {
            setCanCreate(true);
          }
          if (item?.update === "1") {
            setCanEdit(true);
          }
        }
      });
      globalLoader(false);
    } catch (err) {
      logOut(props?.history, "/auth/login");
      globalLoader(false);
    }
  };

  const closeUserModal = async (obj = null) => {
    if (obj) {
      globalLoader(true);
      try {
        let res;
        if (isEdit) {
          res = await updateUserApi(obj);
        } else {
          res = await createUserApi(obj);
        }
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
          callBasicApi();
          if (isEdit) {
            try {
              let res = await profileDetailsApi();
              if (res?.data?.status === 0) {
                profileApiData(res?.data?.data[0]);
              } else {
              }
            } catch (err) {
              if (err?.response?.status === 401) {
                toast.error("Error while fetching profile details");
              }
            }
          }
        } else {
          console.log(res);
          toast.error("Update unsuccessful. Please try again");
        }
        globalLoader(false);
      } catch (err) {
        logOut(props?.history, "auth/login");
        globalLoader(false);
      }
    }
    setIsEdit(false);
    setOpenUserModal(false);
    setFormData(null);
  };

  const openImage = (val, type) => {
    let imgArr = [{ img_url: val }];
    setImageData(imgArr);
    setImgType(type);
    setOpenImageModal(true);
  };

  const closeImageModal = () => {
    setImgType(null);
    setImageData(null);
    setOpenImageModal(false);
  };

  const editUser = (obj) => {
    setFormData(obj);
    setIsEdit(true);
    setOpenUserModal(true);
  };

  const deleteUser = (obj) => {
    setFormData(obj);
    setConfirmModal(true);
  };

  const confirmation = (obj) => {
    if (obj) {
    }
    setConfirmModal(false);
    setFormData(null);
  };

  const openUpdateStatus = (obj) => {
    setFormData(obj);
    setStatusUpdate(true);
  };

  const closeUpdateStatus = async (status) => {
    if (status) {
      let obj = {
        type: "17",
        status: status,
        id: formData?.user_id,
      };

      globalLoader(true);
      try {
        let res = await updateStatusApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
        }
        callBasicApi();
      } catch (err) {
        logOut(props?.history, "auth/login");
        globalLoader(false);
      }
    }
    setStatusUpdate(false);
    setFormData(null);
  };

  const viewCompany = (data, type) => {
    setFormData(data);
    setType(type);
    setOpenViewModal(true);
  };

  const closeViewCompany = () => {
    setFormData(null);
    setType(null);
    setOpenViewModal(false);
  };

  return (
    <>
      <UserHeader />

      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">Users</h3>
                  {canCreate && (
                    <div className="col text-right">
                      <button
                        className="btn btn-sm primary-bg"
                        onClick={() => setOpenUserModal(true)}
                      >
                        Add User
                      </button>
                    </div>
                  )}
                </Row>
              </CardHeader>
              {view ? (
                currentDataList && currentDataList.length > 0 ? (
                  <UserTable
                    statusList={status}
                    tableData={currentDataList}
                    updateStatus={openUpdateStatus}
                    editUser={editUser}
                    deleteUser={deleteUser}
                    openImage={openImage}
                    designationList={designationList}
                    companyList={companyList}
                    canEdit={canEdit}
                    viewCompany={viewCompany}
                  />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>

      {openUserModal && (
        <AddUserModal
          open={openUserModal}
          onCloseModal={closeUserModal}
          isEdit={isEdit}
          formData={formData}
          designationList={designationList}
          companyList={companyList}
          roleList={roleList}
          usageList={usageList}
          emailList={emailList}
        />
      )}

      {statusUpdate && (
        <StatusUpdateModal
          open={statusUpdate}
          onCloseModal={closeUpdateStatus}
          statusList={status}
          formData={formData?.user_status}
        />
      )}

      {openImageModal && (
        <ViewImageModal
          open={openImageModal}
          onCloseModal={closeImageModal}
          data={imageData}
          imgType={imgType}
        />
      )}

      {confirmModal && (
        <ConfirmModal
          open={confirmModal}
          onCloseModal={confirmation}
          id={formData?.user_id}
          name={`${formData?.first_name} ${formData?.last_name}`}
          type="User"
        />
      )}
      {openViewModal && (
        <ViewModal
          open={openViewModal}
          onCloseModal={closeViewCompany}
          data={formData}
          type={type}
        />
      )}
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
  userRights: state?.common?.roleData?.user_rights,
});

export default connect(mapStateProps)(UserList);
