import { globalLoader } from "actions/commonActions";
import UserHeader from "../../components/Headers/userHeader";
import RoadAssistanceTable from "../../components/tables/roadAssistanceTable";
import { sort } from "fast-sort";
import React, { useEffect, useState } from "react";
import { Card, CardHeader, Container, Row } from "reactstrap";
import { logOut } from "_shared/commonFunctions";
import { roadsideAssistanceApi } from "./_actions";
import { connect } from "react-redux";
import DetailsModal from "../../components/Modals/detailsModal";
import { getPopupDetails } from "_shared/commonFunctions";
import { toast } from "react-toastify";

const RoadsideAssistance = (props) => {
  const { currentCompany } = props;
  const [view, setView] = useState(false);
  const [dataList, setDataList] = useState(null);
  const searchFilter = props?.location?.state?.morni_id;
  const searchId = props?.location?.state?.id;
  const [data, setData] = useState(null);
  const [userModal, setUserModal] = useState(false);
  const [detailsTitle, setDetailsTitle] = useState("");

  useEffect(() => {
    callBasicApi();
    // eslint-disable-next-line
  }, [currentCompany]);
  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await roadsideAssistanceApi(currentCompany);
      console.log(res);
      if (res?.data?.status === 0) {
        let arr = sort(res?.data?.data).desc((u) => u?.rsa_requested_date);
        setDataList(arr);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");

        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setDataList(null);
        // toast.error("Data not found");
      } else {
        globalLoader(false);
        toast.error("Server Error");
      }
      setView(true);
      globalLoader(false);
    } catch (err) {
      if (err?.response?.status === 401) {
        logOut(props?.history, "/auth/login");
        globalLoader(false);
      }
      globalLoader(false);
    }
  };

  const customerModalOpen = (obj, type) => {
    if (obj) {
      setData(getPopupDetails(obj, type));
      setDetailsTitle(type);
      setUserModal(true);
    }
  };

  const customerModalClose = () => {
    setData(null);
    setUserModal(false);
    setDetailsTitle("");
  };

  return (
    <>
      <UserHeader />
      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">Roadside Assistance</h3>
                </Row>
              </CardHeader>
              {view ? (
                dataList && dataList.length > 0 ? (
                  <RoadAssistanceTable
                    tableData={dataList}
                    searchFilter={searchFilter}
                    searchId={searchId}
                    customerModalOpen={customerModalOpen}
                  />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
              {userModal && (
                <DetailsModal
                  open={userModal}
                  onCloseModal={customerModalClose}
                  data={data}
                  title={detailsTitle}
                />
              )}
            </Card>
          </div>
        </Row>
      </Container>
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
});

export default connect(mapStateProps)(RoadsideAssistance);
