import { globalLoader } from "actions/commonActions";
import UserHeader from "../../components/Headers/userHeader";
import { sort } from "fast-sort";
import React, { useEffect, useState } from "react";
import { toast } from "react-toastify";
import { Card, CardHeader, Container, Row } from "reactstrap";
import { logOut } from "_shared/commonFunctions";
import StatusUpdateModal from "components/Modals/statusUpdateModal";
import { updateStatusApi } from "./_actions";
import { roleMasterListApi } from "./_actions";
import RoleTable from "../../components/tables/roleTable";
import { roleMasterRequestApi } from "./_actions";
import AddRoleModal from "components/Modals/addRoleModal";
import { connect } from "react-redux";
import { getStatusArray } from "_shared/commonFunctions";

const Role = (props) => {
  const { userRights, currentCompany } = props;
  const [canCreate, setCanCreate] = useState(false);
  const [canEdit, setCanEdit] = useState(false);
  const [openRole, setOpenRole] = useState(false);
  const [view, setView] = useState(false);
  const [dataList, setDataList] = useState(null);
  const [statusUpdate, setStatusUpdate] = useState(false);
  const [formData, setFormData] = useState(null);
  const [status, setStatus] = useState([]);

  useEffect(() => {
    callBasicApi();
    // eslint-disable-next-line
  }, [currentCompany]);

  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await roleMasterListApi(currentCompany);
      if (res?.data?.status === 0) {
        let arr = sort(res?.data?.data).desc((u) => parseInt(u?.role_id));
        setDataList(arr);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");

        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setDataList(null);
      } else {
        globalLoader(false);
      }
      if (res?.data?.status !== 401) {
        let obj = await getStatusArray(14);
        if (obj) {
          setStatus(obj);
        } else {
          toast.error("Error in getting status");
        }
      }
      setView(true);
      userRights?.forEach((item) => {
        if (item?.menu_type_id === "module_20") {
          if (item?.create === "1") {
            setCanCreate(true);
          }
          if (item?.update === "1") {
            setCanEdit(true);
          }
        }
      });
      globalLoader(false);
    } catch (err) {
      if (err?.response?.status === 401) {
        logOut(props?.history, "/auth/login");
        globalLoader(false);
      }
    }
  };

  const openUpdateStatus = (obj) => {
    setFormData(obj);
    setStatusUpdate(true);
  };

  const closeUpdateStatus = async (status) => {
    if (status) {
      let obj = {
        type: "14",
        status: status,
        id: formData?.role_id,
      };
      globalLoader(true);
      try {
        let res = await updateStatusApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
        }
        callBasicApi();
      } catch (err) {
        logOut(props?.history, "auth/login");
        globalLoader(false);
      }
    }
    setStatusUpdate(false);
    setFormData(null);
  };

  const closeRoleModal = async (obj) => {
    if (obj) {
      globalLoader(true);
      try {
        let res = await roleMasterRequestApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
        }
        callBasicApi();
      } catch (err) {
        if (err?.response?.status === 401) {
          logOut(props?.history, "/auth/login");
          globalLoader(false);
        } else {
          globalLoader(false);
        }
      }
    }
    setOpenRole(false);
  };

  return (
    <>
      <UserHeader />
      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">Role</h3>
                  {canCreate && (
                    <div className="col text-right">
                      <button
                        className="btn btn-sm primary-bg"
                        onClick={() => setOpenRole(true)}
                      >
                        Add New Role
                      </button>
                    </div>
                  )}
                </Row>
              </CardHeader>
              {view ? (
                dataList && dataList.length > 0 ? (
                  <RoleTable
                    statusList={status}
                    tableData={dataList}
                    updateStatus={openUpdateStatus}
                    canEdit={canEdit}
                  />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>
      {statusUpdate && (
        <StatusUpdateModal
          open={statusUpdate}
          onCloseModal={closeUpdateStatus}
          statusList={status}
          formData={formData?.role_status}
        />
      )}
      {openRole && (
        <AddRoleModal open={openRole} onCloseModal={closeRoleModal} />
      )}
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
  userRights: state?.common?.roleData?.user_rights,
});

export default connect(mapStateProps)(Role);
