import React, { useEffect, useState } from "react";
import { sort } from "fast-sort";
import ViewImageModal from "components/Modals/viewImageModal";
import ServiceListModal from "components/Modals/serviceListModal";
import ServiceStatusModal from "../../components/Modals/serviceStatusModal";
import UserHeader from "../../components/Headers/userHeader";
import { Card, CardHeader, Container, Row } from "reactstrap";
import { globalLoader } from "actions/commonActions";
import {
  createMaintainance,
  maintenancePortalListApi,
  sendNotificationApi,
} from "./_actions";
import { toast } from "react-toastify";
import MaintenanceTable from "../../components/tables/maintenanceTable";
import { logOut } from "_shared/commonFunctions";
import { updateStatusApi } from "./_actions";
import { connect } from "react-redux";
import DetailsModal from "../../components/Modals/detailsModal";
import { getStatusArray, getPopupDetails } from "../../_shared/commonFunctions";
import { maintenanceLocationApi } from "./_actions";
import ReplyNotification from "../../components/Modals/replyNotificationModal";

import AddMaintenanceModal from "components/Modals/addMaintenanceModal";

const MaintenanceManagement = (props) => {
  const { userRights, currentCompany } = props;
  const [viewImage, setViewImage] = useState(false);
  const [imgType, setImgType] = useState("");
  const [imageData, setImageData] = useState(null);
  const [isEdit, setIsEdit] = useState(false);
  const [openServiceList, setOpenServiceList] = useState(false);
  const [formData, setFormData] = useState(null);
  const [changeStatus, setChangeStatus] = useState(false);
  const [currentDataList, setCurrentDataList] = useState(null);
  const [view, setView] = useState(false);
  const [canEdit, setCanEdit] = useState(false);
  const searchFilter = props?.location?.state?.maint_plate;
  const searchId = props?.location?.state?.id;
  const dropdownFilter = props?.location?.state?.maint_status;
  const dropdownId = props?.location?.state?.dropdownId;
  const [statusList, setStatus] = useState([]);
  const [data, setData] = useState(null);
  const [userModal, setUserModal] = useState(false);
  const [detailsTitle, setDetailsTitle] = useState("");
  const [location, setLocation] = useState([]);
  const [notificationModalOpen, setNotificationModalOpen] = useState(false);

  const [openAddMaintainance, setOpenAddMaintainance] = useState(false);

  useEffect(() => {
    callBasicApi(); // eslint-disable-next-line
  }, [currentCompany]);

  useEffect(() => {
    window.history.replaceState(null, "");
  }, []);

  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await maintenancePortalListApi(currentCompany);
      if (res?.data?.status === 0) {
        let arr = [];
        arr = res?.data?.data;
        let sortArr = sort(arr).desc((u) => new Date(u.prefered_date_time));
        setCurrentDataList(sortArr);
        setView(true);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");

        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setCurrentDataList(null);
        //
      } else {
        globalLoader(false);
      }
      if (res?.data?.status !== 401) {
        globalLoader(true);
        res = await maintenanceLocationApi(currentCompany);
        if (res?.data?.status === 0) {
          let arr = res?.data?.data.map((item) => ({
            value: item?.loc_id,
            label: item?.loc_desc,
          }));
          setLocation(arr);
          globalLoader(false);
        } else if (res?.data?.status === 404) {
          globalLoader(false);
          setView(true);
          setCurrentDataList(null);
          //
        } else {
          globalLoader(false);
        }
      }
      if (res?.data?.status !== 401) {
        let obj = await getStatusArray(1);
        if (obj) {
          setStatus(obj);
        } else {
          toast.error("Error in getting status");
        }
      }

      userRights?.forEach((item) => {
        if (item?.menu_type_id === "module_4") {
          if (item?.update === "1") {
            setCanEdit(true);
          }
        }
      });
      globalLoader(false);
    } catch (err) {
      logOut(props?.history, "/admin/login");
    }
  };

  const openImageModal = (val, type) => {
    setImageData(val);
    setImgType(type);
    setViewImage(true);
  };

  const closeViewImageModal = () => {
    setViewImage(false);
    setImageData(null);
  };

  const openServiceListModal = (val = false, obj = null) => {
    setIsEdit(val);
    setFormData(obj);
    setOpenServiceList(true);
  };

  const closeServiceListModal = (obj = null) => {
    setOpenServiceList(false);
    setIsEdit(false);
    setFormData(null);
  };

  const openChangeStatus = (obj) => {
    setFormData(obj);
    setChangeStatus(true);
  };

  const closeStatus = async (obj = null) => {
    if (obj) {
      globalLoader(true);
      try {
        let res = await updateStatusApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
          toast.error(res?.message);
        }
        callBasicApi();
      } catch (err) {
        logOut(props?.history, "auth/login");
        globalLoader(false);
      }
    }
    setFormData(null);
    setChangeStatus(false);
  };

  const customerModalOpen = (obj, type) => {
    if (obj) {
      setData(getPopupDetails(obj, type));
      setDetailsTitle(type);
      setUserModal(true);
    }
  };

  const customerModalClose = () => {
    setData(null);
    setUserModal(false);
    setDetailsTitle("");
  };

  const openNotificationModal = (obj) => {
    let notiData = {
      user_id: obj.userid,
      tag: obj?.company_name,
      type: "1",
      typeDesc: "Service",
    };
    setFormData(notiData);
    setNotificationModalOpen(true);
  };

  const closeNotificationModal = async (obj = null) => {
    if (obj) {
      globalLoader(true);
      try {
        let res = await sendNotificationApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
          toast.error(res?.data?.message);
        }
      } catch (err) {
        toast.error("Error while posting notification");
        console.log(err);
      }
      globalLoader(false);
      callBasicApi();
    }
    setFormData(null);
    setNotificationModalOpen(false);
  };

  const addMaintaiananceHandler = async (data) => {
    setOpenAddMaintainance(false);
    if (data) {
      globalLoader(true);
      try {
        let res = await createMaintainance(data);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
          toast.error(res?.data?.message);
        }
      } catch (err) {
        toast.error("Error while posting notification");
        console.log(err);
      }
      globalLoader(false);
      callBasicApi();
    }
  };

  return (
    <>
      <UserHeader />
      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">Repair and Maintenance</h3>
                  <div className="col text-right">
                    <button
                      className="btn btn-sm primary-bg"
                      onClick={() => setOpenAddMaintainance(true)}
                    >
                      Add Maintainance
                    </button>
                  </div>
                </Row>
              </CardHeader>
              {view ? (
                currentDataList && currentDataList.length > 0 ? (
                  <MaintenanceTable
                    statusList={statusList}
                    tableData={currentDataList}
                    openServiceListModal={openServiceListModal}
                    changeStatus={openChangeStatus}
                    isEdit={isEdit}
                    openImage={openImageModal}
                    searchFilter={searchFilter}
                    searchId={searchId}
                    dropdownFilter={dropdownFilter}
                    dropdownId={dropdownId}
                    canEdit={canEdit}
                    customerModalOpen={customerModalOpen}
                    sendNotification={openNotificationModal}
                  />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>
      {viewImage && (
        <ViewImageModal
          open={viewImage}
          onCloseModal={closeViewImageModal}
          data={imageData}
          imgType={imgType}
        />
      )}
      {openServiceList && (
        <ServiceListModal
          open={openServiceList}
          onCloseModal={closeServiceListModal}
        />
      )}
      {openAddMaintainance && (
        <AddMaintenanceModal
          open={openAddMaintainance}
          onCloseModal={addMaintaiananceHandler}
        />
      )}
      {changeStatus && (
        <ServiceStatusModal
          open={changeStatus}
          onCloseModal={closeStatus}
          statusList={statusList}
          formData={formData}
          location={location}
        />
      )}
      {userModal && (
        <DetailsModal
          open={userModal}
          onCloseModal={customerModalClose}
          data={data}
          title={detailsTitle}
        />
      )}
      {notificationModalOpen && (
        <ReplyNotification
          open={notificationModalOpen}
          onCloseModal={closeNotificationModal}
          formData={formData}
        />
      )}
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
  userRights: state?.common?.roleData?.user_rights,
});

export default connect(mapStateProps)(MaintenanceManagement);
