import UserHeader from "../../components/Headers/userHeader";
import React, { useEffect, useState } from "react";
import { Card, CardHeader, Container, Row } from "reactstrap";
import AddServiceCenterModal from "../../components/Modals/addServiceCenterModal";
import ConfirmModal from "components/Modals/confirmModal";
import { globalLoader } from "actions/commonActions";
import { toast } from "react-toastify";
import { logOut } from "_shared/commonFunctions";
import { locationsApi } from "./_actions";
import { sort } from "fast-sort";
import ServiceCenterTable from "../../components/tables/serviceCenterTable";
import { updatePortalLocation } from "./_actions";
import { createPortalLocation } from "./_actions";
import { updateStatusApi } from "./_actions";
import StatusUpdateModal from "components/Modals/statusUpdateModal";
import { connect } from "react-redux";
import { getStatusArray } from "_shared/commonFunctions";

const ServiceCenterManagement = (props) => {
  const { userRights, currentCompany } = props;
  const [canCreate, setCanCreate] = useState(false);
  const [canEdit, setCanEdit] = useState(false);
  const [openServiceCenterModal, setOpenServiceCenterModal] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [formData, setFormData] = useState("");
  const [openConfirmModal, setOpenConfirmModal] = useState(false);
  const [view, setView] = useState(false);
  const [currentDataList, setCurrentDataList] = useState(null);
  const [statusUpdate, setStatusUpdate] = useState(false);
  const [status, setStatus] = useState([]);

  useEffect(() => {
    callBasicApi();
    // eslint-disable-next-line
  }, [currentCompany]);

  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await locationsApi(currentCompany);
      if (res?.data?.status === 0) {
        let arr = sort(res?.data?.data).desc((u) => u?.loc_id);
        setCurrentDataList(arr);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");

        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setCurrentDataList(null);
        //
      } else {
        globalLoader(false);
      }
      if (res?.data?.status !== 401) {
        let obj = await getStatusArray(16);
        if (obj) {
          setStatus(obj);
        } else {
          toast.error("Error in getting status");
        }
      }
      setView(true);
      userRights?.forEach((item) => {
        if (item?.menu_type_id === "module_8") {
          if (item?.create === "1") {
            setCanCreate(true);
          }
          if (item?.update === "1") {
            setCanEdit(true);
          }
        }
      });
      globalLoader(false);
    } catch (err) {
      if (err?.response?.status === 401) {
        logOut(props?.history, "/auth/login");
        globalLoader(false);
      } else {
        globalLoader(false);
      }
    }
  };

  const addServiceCenter = (obj = null) => {
    if (obj) {
      setIsEdit(true);
      setFormData(obj);
      setOpenServiceCenterModal(true);
    }
  };

  const closeServiceCenterModal = async (obj = null) => {
    if (obj) {
      try {
        let res;
        if (isEdit) {
          res = await updatePortalLocation(obj);
        } else {
          res = await createPortalLocation(obj);
        }
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
          callBasicApi();
        } else {
        }
      } catch (err) {
        if (err?.response?.status === 401) {
          logOut(props?.history, "/auth/login");
          globalLoader(false);
        } else {
          globalLoader(false);
        }
      }
    }
    setIsEdit(false);
    setFormData(null);
    setOpenServiceCenterModal(false);
  };

  const closeConfirmModal = (val) => {
    if (val) {
    }
    setFormData(null);
    setOpenConfirmModal(false);
  };

  const openUpdateStatus = (obj) => {
    setFormData(obj);
    setStatusUpdate(true);
  };

  const closeUpdateStatus = async (status) => {
    if (status) {
      let obj = {
        type: "16",
        status: status,
        id: formData?.loc_id,
      };
      globalLoader(true);
      try {
        let res = await updateStatusApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
        }
        callBasicApi();
      } catch (err) {
        logOut(props?.history, "auth/login");
        globalLoader(false);
      }
    }
    setStatusUpdate(false);
    setFormData(null);
  };

  return (
    <>
      <UserHeader />
      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">Service Center</h3>
                  {canCreate && (
                    <div className="col text-right">
                      <button
                        className="btn btn-sm primary-bg"
                        onClick={() => setOpenServiceCenterModal(true)}
                      >
                        Add Service Center
                      </button>
                    </div>
                  )}
                </Row>
              </CardHeader>
              {view ? (
                currentDataList && currentDataList.length > 0 ? (
                  <ServiceCenterTable
                    statusList={status}
                    tableData={currentDataList}
                    editData={addServiceCenter}
                    updateStatus={openUpdateStatus}
                    canEdit={canEdit}
                  />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>
      {openServiceCenterModal && (
        <AddServiceCenterModal
          isEdit={isEdit}
          formData={formData}
          open={openServiceCenterModal}
          onCloseModal={closeServiceCenterModal}
        />
      )}

      {openConfirmModal && (
        <ConfirmModal
          open={openConfirmModal}
          onCloseModal={closeConfirmModal}
          id={formData?.code}
        />
      )}
      {statusUpdate && (
        <StatusUpdateModal
          open={statusUpdate}
          onCloseModal={closeUpdateStatus}
          statusList={status}
          formData={formData?.loc_status}
        />
      )}
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
  userRights: state?.common?.roleData?.user_rights,
});

export default connect(mapStateProps)(ServiceCenterManagement);
