import React, { useState, useEffect } from "react";
import { sort } from "fast-sort";
import { Card, CardHeader, Container, Row } from "reactstrap";
import { connect } from "react-redux";
import { toast } from "react-toastify";
import UserHeader from "../../components/Headers/userHeader";
import NotificationTable from "../../components/tables/notificationTable";
import { globalLoader } from "../../actions/commonActions";
import { logOut, getPopupDetails } from "../../_shared/commonFunctions";
import SendNotificationModal from "../../components/Modals/sendNotificationModal";
import {
  userNotificationPortalListApi,
  sendNotificationApi,
  notificationListApi,
  appUserListApi,
  companyListApi,
} from "./_actions";
import DetailsModal from "../../components/Modals/detailsModal";

const NotificationManagement = (props) => {
  const { currentCompany } = props;
  const [dataList, setDataList] = useState(null);
  const [view, setView] = useState(false);
  const [openNotificationModal, setOpenNotificationModal] = useState(false);
  const [data, setData] = useState(null);
  const [userModal, setUserModal] = useState(false);
  const [detailsTitle, setDetailsTitle] = useState("");
  const [statusList, setStatusList] = useState([]);
  const [notificationList, setNotificationList] = useState(null);
  const [userOptions, setUserOptions] = useState(null);
  const [companyOptions, setCompanyOptions] = useState(null);

  useEffect(() => {
    callBasicApi(); // eslint-disable-next-line
  }, [currentCompany]);

  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await userNotificationPortalListApi(currentCompany);
      if (res?.data?.status === 0) {
        let arr = sort(res?.data?.data).desc((u) => parseInt(u?.nti_descid));
        setDataList(arr);
        setStatusList([
          { id: "0", name: "False" },
          { id: "1", name: "True" },
        ]);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");
        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setDataList(null);
        //
      }
      if (res?.data?.status !== 401) {
        res = await notificationListApi();
        if (res?.data?.status === 0) {
          let arr = [];
          res?.data?.data.length > 0 &&
            res?.data?.data?.forEach((item) => {
              arr.push({
                label: `${item?.nti_type_desc}`,
                value: `${item?.nti_type_id}`,
              });
            });
          setNotificationList(arr);
        } else {
          logOut(props?.history, "/auth/login");
          globalLoader(false);
        }
      }
      if (res?.data?.status !== 401) {
        res = await appUserListApi();
        if (res?.data?.status === 0) {
          let arr = [];
          res?.data?.data.length > 0 &&
            res?.data?.data.forEach((item) => {
              arr.push({
                label: `${item.user_id} - ${item.first_name} ${item.last_name}`,
                value: `${item.user_id}`,
              });
            });
          setUserOptions(arr);
        }
      }
      if (res?.data?.status !== 401) {
        res = await companyListApi();
        if (res?.data?.status === 0) {
          let arr = [];
          res?.data?.data.length > 0 &&
            res?.data?.data.forEach((item) => {
              arr.push({
                label: `${item.company_name}`,
                value: `${item.company_code}`,
              });
            });
          setCompanyOptions(arr);
        }
      }

      setView(true);
      globalLoader(false);
    } catch (err) {
      if (err?.response?.status === 401) {
        logOut(props?.history, "/auth/login");
        globalLoader(false);
      } else {
        globalLoader(false);
      }
    }
  };

  const notificationModalClose = async (obj) => {
    if (obj) {
      globalLoader(true);
      try {
        let res = await sendNotificationApi(obj);
        if (res?.data?.status === 0) {
          toast.success(res?.data?.message);
        } else {
          toast.error(res?.data?.message);
        }
      } catch (err) {
        toast.error("Error while posting notification");
        console.log(err);
      }
      globalLoader(false);
      callBasicApi();
    }
    setOpenNotificationModal(false);
  };

  const customerModalOpen = (obj, type) => {
    if (obj) {
      setData(getPopupDetails(obj, type));
      setDetailsTitle(type);
      setUserModal(true);
    }
  };

  const customerModalClose = () => {
    setData(null);
    setUserModal(false);
    setDetailsTitle("");
  };

  return (
    <>
      <UserHeader />
      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">
                    Notification Management
                  </h3>
                  <div className="col text-right">
                    <button
                      className="btn btn-sm primary-bg"
                      onClick={() => setOpenNotificationModal(true)}
                    >
                      Send Notification
                    </button>
                  </div>
                </Row>
              </CardHeader>
              {view ? (
                dataList && dataList.length > 0 ? (
                  <NotificationTable
                    statusList={statusList}
                    tableData={dataList}
                    customerModalOpen={customerModalOpen}
                    notificationList={notificationList}
                  />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>
      {openNotificationModal && (
        <SendNotificationModal
          open={openNotificationModal}
          onCloseModal={notificationModalClose}
          notificationList={notificationList}
          userOptions={userOptions}
          companyOptions={companyOptions}
        />
      )}
      {userModal && (
        <DetailsModal
          open={userModal}
          onCloseModal={customerModalClose}
          data={data}
          title={detailsTitle}
        />
      )}
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
});

export default connect(mapStateProps)(NotificationManagement);
