import React, { useEffect, useState } from "react";
import { Pie } from "react-chartjs-2";
import { Card, CardBody, CardHeader } from "reactstrap";

const FleetCard = (props) => {
  const { fleetBookingCard } = props;
  const [currentIndex, setCurrentIndex] = useState(0);
  const [data, setData] = useState(null);

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        display: false,
      },
    },
  };

  useEffect(() => {
    setGraphData(); // eslint-disable-next-line
  }, [currentIndex]);

  const setGraphData = () => {
    if (fleetBookingCard) {
      const currData = {
        labels: [
          fleetBookingCard[currentIndex]?.label1,
          fleetBookingCard[currentIndex]?.label2,
          fleetBookingCard[currentIndex]?.label3,
          fleetBookingCard[currentIndex]?.label4,
        ],
        datasets: [
          {
            data: [
              fleetBookingCard[currentIndex]?.nos1,
              fleetBookingCard[currentIndex]?.nos2,
              fleetBookingCard[currentIndex]?.nos3,
              fleetBookingCard[currentIndex]?.nos4,
            ],
            backgroundColor: [
              "rgb(13 201 163)",
              "rgb(17, 103, 177)",
              "rgb(255 136 0)",
              "rgb(212 0 42)",
            ],
            borderWidth: 1,
          },
        ],
      };
      setData(currData);
    }
  };

  return (
    <Card className="dashboard-card-own fleet-card-wrapper">
      <CardHeader className="d-flex justify-content-between align-items-center border-bottom-0">
        <span className="font-13 weight-600 color-primary">Current Fleet</span>
        <button
          className="btn btn-fleet light-bg"
          onClick={() => props?.history?.push("/admin/vehicle_contract_driver")}
        >
          Manage Fleet <i className="fas fa-arrow-right"></i>
        </button>
      </CardHeader>
      <CardBody>
        {fleetBookingCard && fleetBookingCard.length > 0 ? (
          <>
            <div className="graph-content-wrapper">
              <div className="graph-wrapper">
                {data && <Pie data={data} options={options} />}
              </div>
              <div className="table-responsive fleet-table-wrapper pl-1">
                <table className="fleet-table">
                  <thead>
                    <tr className="color-primary weight-60">
                      <th>{fleetBookingCard[currentIndex]?.label0}</th>
                      <th className="text-right">
                        {fleetBookingCard[currentIndex]?.nos0}
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>
                        <i className="fas fa-circle color-green pr-2"></i>
                        {fleetBookingCard[currentIndex]?.label1}
                      </td>
                      <td className="text-right">
                        {fleetBookingCard[currentIndex]?.nos1}
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <i className="fas fa-circle color-blue pr-2"></i>
                        {fleetBookingCard[currentIndex]?.label2}
                      </td>
                      <td className="text-right">
                        {fleetBookingCard[currentIndex]?.nos2}
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <i className="fas fa-circle color-orange pr-2"></i>
                        {fleetBookingCard[currentIndex]?.label3}
                      </td>
                      <td className="text-right">
                        {fleetBookingCard[currentIndex]?.nos3}
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <i className="fas fa-circle color-primary pr-2"></i>
                        {fleetBookingCard[currentIndex]?.label4}
                      </td>
                      <td className="text-right">
                        {fleetBookingCard[currentIndex]?.nos4}
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
            <div className="d-flex justify-content-between pt-4 font-12">
              <div className="pt-1">
                <i
                  className={`fas fa-arrow-left pr-1 ${
                    currentIndex > 0 && "color-primary cursor-pointer"
                  }`}
                  onClick={() => {
                    if (currentIndex > 0) setCurrentIndex(currentIndex - 1);
                  }}
                ></i>
                <i
                  className={`fas fa-arrow-right px-2 ${
                    currentIndex < fleetBookingCard.length - 1 &&
                    "color-primary cursor-pointer"
                  }`}
                  onClick={() => {
                    if (currentIndex < fleetBookingCard.length - 1)
                      setCurrentIndex(currentIndex + 1);
                  }}
                ></i>
                <span className="text-muted pl-1">
                  {currentIndex + 1} / {fleetBookingCard.length}
                </span>
              </div>
              <div
                className="color-primary weight-800 cursor-pointer font-13 pt-1"
                onClick={() => props?.history?.push("/admin/car_replacement")}
              >
                Replacement/Pool Vehicles <i className="fas fa-arrow-right"></i>
              </div>
            </div>
          </>
        ) : (
          <div className="mt-5 pt-4 color-primary d-flex justify-content-center align-items-center">
            Data Not Available
          </div>
        )}
      </CardBody>
    </Card>
  );
};

export default FleetCard;
