import React, { useEffect, useState } from "react";
import UserHeader from "../../components/Headers/userHeader";
import { Card, CardHeader, Container, Row } from "reactstrap";
import MaintenanceHistoryTable from "../../components/tables/maintenanceHistoryTable";
import { logOut } from "_shared/commonFunctions";
import { globalLoader } from "actions/commonActions";
import { maintenanceHistoryListApi } from "./_actions";
import { sort } from "fast-sort";
import { connect } from "react-redux";

const MaintenanceHistory = (props) => {
  const { currentCompany } = props;
  const [dataList, setDataList] = useState(null);
  const [view, setView] = useState(false);

  useEffect(() => {
    callBasicApi(); // eslint-disable-next-line
  }, [currentCompany]);

  const callBasicApi = async () => {
    setView(false);
    globalLoader(true);
    try {
      let res = await maintenanceHistoryListApi(currentCompany);
      if (res?.data?.status === 0) {
        let arr = sort(res?.data?.data).desc((u) => new Date(u?.create_date));
        setDataList(arr);
      } else if (res?.data?.status === 401) {
        logOut(props?.history, "/auth/login");

        globalLoader(false);
      } else if (res?.data?.status === 404) {
        globalLoader(false);
        setView(true);
        setDataList(null);
        //
      } else {
      }
      setView(true);
      globalLoader(false);
    } catch (err) {
      if (err?.response?.status === 401) {
        logOut(props?.history, "/auth/login");
        globalLoader(false);
      } else {
        globalLoader(false);
      }
    }
  };

  return (
    <>
      <UserHeader />
      <Container className="mt--8" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <h3 className="mb-0 color-primary">Maintenance History</h3>
                  {/* <div className="col text-right">
                    <button className="btn btn-sm primary-bg">
                      Add New Record
                    </button>
                  </div> */}
                </Row>
              </CardHeader>
              {view ? (
                dataList && dataList.length > 0 ? (
                  <MaintenanceHistoryTable tableData={dataList} />
                ) : (
                  <div className="px-3 pt-3 pb-3">
                    <div className="no-data-available color-primary">
                      Data Not Available
                    </div>
                  </div>
                )
              ) : null}
            </Card>
          </div>
        </Row>
      </Container>
    </>
  );
};

const mapStateProps = (state) => ({
  currentCompany: state?.common?.currentCompany?.value,
});

export default connect(mapStateProps)(MaintenanceHistory);
